import { TokenContract } from './TokenContract'
import { CakeLPContract } from './CakeLPContract'

export class Connector {
  constructor(w3) {
    this.w3 = w3
  }

  async isHealthy() {
    try {
      await this.w3.getBlockNumber()
      return true
    } catch {
      return false
    }
  }

  async getBlockNumber() {
    return this.w3.eth.getBlockNumber()
  }

  async getBlockWithTransactions(blockNumber) {
    return this.w3.eth.getBlock(blockNumber, true)
  }

  async getTransactionReceipt(transactionHash) {
    return this.w3.eth.getTransactionReceipt(transactionHash)
  }

  createw3Contract(abi, contractAddress) {
    const w3contract = new this.w3.eth.Contract(abi, contractAddress)
    return w3contract
  }

  async createTokenContract(abi, contractAddress) {
    const w3contract = this.createw3Contract(abi, contractAddress)
    const contract = new TokenContract(w3contract)
    return contract
  }

  async createCakeLpContract(abi, contractAddress) {
    const w3contract = this.createw3Contract(abi, contractAddress)
    const contract = new CakeLPContract(w3contract)
    return contract
  }
}
