import { Grid, makeStyles } from "@material-ui/core";
import { TokenAssessmentTitleToken } from "./TokenAssessmentTitleToken";
import { TokenAssessmentTitleRisk } from "./TokenAssessmentTitleRisk";

const useStyles = makeStyles(() => ({
  "@keyframes slideButtonBackgroundAssessmentTitle": {
    "0%": {
      backgroundPosition: "0% 50%",
    },
    "50%": {
      backgroundPosition: "100% 50%",
    },
    "100%": {
      backgroundPosition: "0% 50%",
    },
  },
  titleBackground: {
    background: "linear-gradient(-45deg, #e73c7e, #23d5ab, #231530, #0b1e20)",
    backgroundSize: "400% 400%",
    animation: "$slideButtonBackgroundAssessmentTitle 15s ease infinite",
  },
}));

export const TokenAssessmentTitle = ({ symbol, name, riskLevel, onClose }) => {
  const classes = useStyles();

  return (
    <Grid container xs={12} className={classes.titleBackground}>
      <Grid item xs={12} sm={8}>
        <TokenAssessmentTitleToken symbol={symbol} name={name} />
      </Grid>
      <Grid item xs={12} sm={4}>
        <TokenAssessmentTitleRisk
          riskLevelScore={riskLevel.score + 1}
          riskLevelName={riskLevel.label}
          onCloseClick={onClose}
        />
      </Grid>
    </Grid>
  );
};
