import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles((theme) => ({
  tokenCardLevelContainer: {
    marginLeft: "auto",
    marginRight: "auto",
    textAlign: "center",
    height: "35px",
    width: "35px",
    borderRadius: "50%",
  },
  spoonLevel1: {
    backgroundColor: "#ca171e",
  },
  spoonLevel2: {
    backgroundColor: "#f0a3a6",
  },
  spoonLevel3: {
    backgroundColor: "#f0eca1",
  },
  spoonLevel4: {
    backgroundColor: "#4e6640",
  },
  spoonLevel5: {
    backgroundColor: "#e7ffd9",
    color: "black",
  },
  levelText: {
    fontSize: "23px",
  },
}));

export const TokenCardLevel = ({ children }) => {
  const classes = useStyles();

  return (
    <div
      className={[
        classes.tokenCardLevelContainer,
        classes[`spoonLevel${children}`],
      ].join(" ")}
    >
      <Typography className={classes.levelText}>{children}</Typography>
    </div>
  );
};
