import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles(() => ({
  container: {
    marginLeft: "auto",
    marginRight: "auto",
    display: "flex",
    padding: "20px",
  },
  symbolDiv: {
    marginRight: 30,
  },
  symbolText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  symbolName: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    fontSize: "10px",
  },
  nameDiv: {
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column",
  },
}));

export const TokenAssessmentTitleToken = ({ symbol, name }) => {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      <div className={classes.symbolDiv}>
        <Typography className={classes.symbolText} variant="h3">
          {symbol}
        </Typography>
      </div>
      <div className={classes.nameDiv}>
        <Typography className={classes.nameText}>{name}</Typography>
      </div>
    </div>
  );
};
