import React, { useState } from "react";
import Typography from "@mui/material/Typography";
import WatcherGrid from "./Grid";
import { FilterPanel } from "./FilterPanel";
import { TokenHandlerConsumer } from "../../../lib/tokenHandler";
import { NEW_TOKEN_EVENT_TYPE_ID } from "../../../lib/constants";
import { LoadingProcess } from "../../common/Loadings";

const Watcher = () => {
  const [filters, setFilters] = useState([]);

  return (
    <>
      <TokenHandlerConsumer>
        {(value) => {
          const { tokens, tokenSelected, onOpenCard, errorMessage } = value;
          const tokenFiltered = tokens.filter(
            (token) =>
              (token.events[0].eventType.identifier !==
                NEW_TOKEN_EVENT_TYPE_ID ||
                token.events.length > 1) &&
              (filters.length === 0 ||
                filters.some((x) => x.id === token.riskLevel.score))
          );
          return (
            <div className="App">
              <header className="App-header">
                <p>{errorMessage}</p>
                <Typography variant="h3" component="h3" marginBottom="20px">
                  SPOON Watcher
                </Typography>
                <FilterPanel setFilters={setFilters} filters={filters} />
                <br />
                {tokenFiltered.length === 0 ? (
                  <LoadingProcess />
                ) : (
                  <WatcherGrid
                    tokens={tokenFiltered}
                    tokenSelected={tokenSelected}
                    onOpenCard={onOpenCard}
                  />
                )}
              </header>
            </div>
          );
        }}
      </TokenHandlerConsumer>
    </>
  );
};

export default Watcher;
