export class TokenContract {
  constructor(contract) {
    this.contract = contract

    this.symbol = undefined
    this.name = undefined
  }

  async isTokenContract() {
    try {
      await this.getName()
      await this.getSymbol()

      return true
    } catch {
      return false
    }
  }

  async isMintable() {
    try {
      await this.contract.methods.mint(0).estimateGas()
    } catch (error) {
      return error.message === 'Returned error: execution reverted: Ownable: caller is not the owner' ? true : false
    }
  }

  async getName() {
    if (!this.name) {
      this.name = await this.contract.methods.name().call()
    }

    return this.name
  }

  async getSymbol() {
    if (!this.symbol) {
      this.symbol = await this.contract.methods.symbol().call()
    }

    return this.symbol
  }

  async getDescription() {
    const name = await this.getName()
    const symbol = await this.getSymbol()

    return `${symbol} - ${name}`
  }
}
