import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import CoinSpin from '../../../img/CoinSpin.gif';
import SpoonRich from '../../../img/SpoonRich.gif';

const useStyles = makeStyles((theme) => ({
  centered: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingTop: 50,
    paddingBottom: 90,
  },
  spoonimg: {
    width:'125px',
    height: '200px',
    marginLeft: '-260px',
    position:'absolute'
  },
  spincoin:{
    width: '85px',
    marginTop: '5px',
    marginLeft: '250px',
    position:'absolute'
  },
}));

export const LoadingProcess = () => {
  const classes = useStyles();
    return (
    <div className={classes.centered} >
      <img src={CoinSpin} className={classes.spincoin} alt='SpinCoin' />
      <Typography >
        Seeking investment opportunities. Please wait...
      </Typography>
      <img src={SpoonRich} className={classes.spoonimg} alt='SpoonIcon' />
    </div>
    );
}