import React from "react";
import Stack from "@mui/material/Stack";
import { Typography } from "@material-ui/core";
import { TokenAssessmentContentContainer } from "../TokenAssessmentContentContainer";
import { TokenAssessmentTip } from "../TokenAssessmentTip";
import { useMediaQuery } from "react-responsive";

export const WarningsTab = ({ tips }) => {
  const isSmallScreen = useMediaQuery({ query: `(max-width: 1460px)` });
  const padding = isSmallScreen ? 1 : 20;

  return (
    <TokenAssessmentContentContainer>
      <Stack spacing={2} sx={{ pl: padding, pr: padding }}>
        <Typography variant="h6">
          These are general warnings about trading in the decentralised
          ecosystem
        </Typography>
        {tips.length &&
          tips.map((tip) => (
            <TokenAssessmentTip title={null} height={"auto"}>
              {tip}
            </TokenAssessmentTip>
          ))}
      </Stack>
    </TokenAssessmentContentContainer>
  );
};
