import React from 'react'
import * as _ from 'lodash'
import { TokenHandler } from './TokenHandler'
import { GlobalState } from '../global'

const TokenHandlerContext = React.createContext()

class TokenHandlerProvider extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      tokens: [],
      tokenSelected: ''
    }

    GlobalState.TokenHandler = new TokenHandler(
      this.#getTokens,
      this.#setTokens,
    )
  }

  #getTokens = () => _.clone(this.state.tokens, false)

  #setTokens = (tokens) => {
    this.setState(() => ({
      tokens,
    }))
  }

  onOpenCard = (tokenSelected) => {
    const newTokenSelected =
      this.state.tokenSelected === tokenSelected ? '' : tokenSelected
      
    const tokens = this.state.tokens;
    const index = tokens.findIndex(x => x.contractAddress === tokenSelected)
    tokens[index].newEvent = false

    this.setState({
      tokens: tokens,
      tokenSelected: newTokenSelected,
    })
  }

  render() {
    return (
      <TokenHandlerContext.Provider
        value={{
          ...this.state,
          onOpenCard: this.onOpenCard,
        }}
      >
        {this.props.children}
      </TokenHandlerContext.Provider>
    )
  }
}

const TokenHandlerConsumer = TokenHandlerContext.Consumer

export { TokenHandlerProvider, TokenHandlerConsumer, TokenHandlerContext }
