import React from "react";
import { Typography, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { TokenCardLevel } from "./TokenCardLevel";

const useStyles = makeStyles(() => ({
  container: {
    marginLeft: "auto",
    marginRight: "auto",
    padding: "20px",
    display: "flex",
    flexWrap: "wrap",
    flexDirection: "row",
    justifyContent: "flex-end",
    alignItems: "flex-end",
    alignContent: "start",
  },
  scoreDiv: {
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column",
    flex: "0 0 auto",
    margin: "10px",
  },
  name: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  nameDiv: {
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column",
    flex: "0 0 auto",
    marginTop: "auto",
    marginBottom: "auto",
  },
  closeDiv: {
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column",
    flex: "1 1 auto",
    marginLeft: "0px",
    marginRight: "50px",
    marginBottom: "auto",
    marginTop: "auto",
    maxWidth: "10px",
  },
}));

export const TokenAssessmentTitleRisk = ({
  riskLevelScore,
  riskLevelName,
  onCloseClick,
}) => {
  const classes = useStyles();

  return (
    <div className={classes.container}>
      <div className={classes.nameDiv}>
        <Typography className={classes.name}>{riskLevelName}</Typography>
      </div>
      <div className={classes.scoreDiv}>
        <TokenCardLevel>{riskLevelScore}</TokenCardLevel>
      </div>
      <div className={classes.closeDiv}>
        <Button
          onClick={onCloseClick}
          style={{
            fontSize: "15px",
            maxWidth: "20px",
          }}
          color="textSecondary"
        >
          X
        </Button>
      </div>
    </div>
  );
};
