const showNewWatchedToken = (symbol) => {
  GlobalState.Observer.emitOpenSnackbar(
    `[Watcher] ${symbol} - New token under SPOON Watcher`,
    "success",
    5000
  );
};

export const GlobalState = {
  AppComponent: null,
  Voyager: null,
  History: null,
  SiteTheme: null,
  TokenHandler: null,
  Observer: new Observer(),
  showExplorerEvent,
  showNewWatchedToken,
  getI10n: () => GlobalState.AppComponent.state.i10n,
  fromI10n: (key) => GlobalState.getI10n()[key],
  WhitelistedAddresses: null,
};

export const { fromI10n } = GlobalState;
