import React from "react";
import { Typography, Button } from "@material-ui/core";

export const TradeLink = (props) => {
  const { href, img, className, alt, title } = props;
  return (
    <Button href={href} target="_blank" rel="noopener">
      <Typography>{title}&nbsp;&nbsp;</Typography>
      <img src={img} className={className} alt={alt} />
    </Button>
  );
};