import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Alpha } from "./Alpha";
import { Alpha11 } from "./Alpha11";
import { Alpha12 } from "./Alpha12";
import { Typography, Box, Grid } from "@material-ui/core";
import DropDown from "../../common/DropDown";
import { ChangelogList } from "./ChangelogList";

const useStyles = makeStyles(() => ({
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    height: `100%`,
  },
  version: {
    width: 800,
    marginBottom: "10px",
  },
}));

const Changelog = () => {
  const classes = useStyles();

  return (
    <div>
      <div className={classes.container}>
        <Typography>
          {" "}
          <h1> List of Changelogs </h1>{" "}
        </Typography>
        {ChangelogList.map((changelog) => (
          <Grid className={classes.version}>
            <DropDown name={changelog.version}>
              <Typography>
                {changelog.changes.map((change) => (
                  <Box>
                    <change.iconComponent /> - {change.changeDescription}
                  </Box>
                ))}
              </Typography>
            </DropDown>
          </Grid>
        ))}
        <Alpha12 className={classes.version} />
        <Alpha11 className={classes.version} />
        <Alpha className={classes.version} />
      </div>
    </div>
  );
};

export default Changelog;
