import { Token } from '../models'
import { GlobalState } from '../global'
import {
  NEW_TOKEN_EVENT_TYPE_ID,
  LIQUIDITY_CREATION_EVENT_TYPE_ID,
  LIQUIDITY_BURNED_EVENT_TYPE_ID,
} from '../constants'

export class TokenHandler {
  #getTokens
  #setTokens

  constructor(getTokens, setTokens) {
    this.#getTokens = getTokens
    this.#setTokens = setTokens
  }

  create = (
    address,
    LPAddress,
    symbol,
    name,
    isMintable,
    transactionHash,
    blockNumber,
    eventType,
  ) => {
    const token = new Token(address, LPAddress, symbol, name, isMintable)
    token.addNewEvent(transactionHash, blockNumber, eventType)
    this.#handleToken(token)
  }

  #handleToken = (tokenToHandle) => {
    const tokens = this.#getTokens()
    const tokenAdded = tokens.some(
      (x) => x.contractAddress === tokenToHandle.contractAddress,
    )
    const event = tokenToHandle.events[0]
    if (tokenAdded) {
      let token = tokens.find(
        (x) => x.contractAddress === tokenToHandle.contractAddress,
      )
      this.#handleEvent(tokens, token, event, tokenToHandle.LPAddress)
    } else {
      this.#addToken(tokens, tokenToHandle)
    }
  }

  #handleEvent = (tokens, token, event, lPAddress) => {
    const eventAdded = token.events.some((x) => x.txHash === event.txHash)
    if (!eventAdded) {
      if (
        (event.eventType.identifier === LIQUIDITY_CREATION_EVENT_TYPE_ID ||
          event.eventType.identifier === LIQUIDITY_BURNED_EVENT_TYPE_ID) &&
        !token.LPAddress
      ) {
        token.LPAddress = lPAddress
      }
      this.#updateToken(tokens, token, event)
    }
  }

  #addToken = (tokens, token) => {
    tokens.push(token)
    this.#sendNotification(token)
    this.#setTokens(tokens)
  }

  #updateToken = (tokens, token, event) => {
    const index = tokens.find(
      (x) => x.contractAddress === token.contractAddress,
    )
    token.pushEvent(event)
    tokens[index] = token
    this.#sendNotification(token)
    this.#setTokens(tokens)
  }

  #sendNotification = (token) => {
    const isNotNewTokenEvent =
      token.events[0].eventType.identifier !== NEW_TOKEN_EVENT_TYPE_ID &&
      token.events.length === 1
    const hasMultipleEvents = token.events.length === 2

    const sendNotification = isNotNewTokenEvent || hasMultipleEvents
    if (sendNotification) {
      GlobalState.showNewWatchedToken(token.symbol)
      GlobalState.Observer.emitSoundNotification()
    }
  }
}
