import PancakeLogo from "../../../../../img/PancakeLogo.svg";
import { TokenCardBuyAtButton } from "./TokenCardBuyAtButton";

export const TokenCardBuyAtPancakeButton = ({ contractAddress }) => {
  const href = `https://pancakeswap.finance/swap?outputCurrency=${contractAddress}`;

  return <TokenCardBuyAtButton href={href} exchangeImage={PancakeLogo} />;
};
