import React from "react";
import { Route, Switch, HashRouter } from "react-router-dom";
import MainLayout from "./components/template/MainLayout";
import { HomePage } from "./components/pages/Home";
import Watcher from "./components/pages/Watcher";
import Explorer from "./components/pages/Explorer";
import NotFound from "./components/pages/Errors/404";
import ServiceLevelIndicator from "./components/pages/ServiceLevelIndicator";
import Warning from "./components/pages/Warning";
import Changelog from "./components/pages/Changelog";
import { homeLink,watcherLink,explorerLink,serviceLink,warningLink,ChangelogLink } from "./lib/constants";

const Router = ({ children }) => {
  return (
    <HashRouter>
      <MainLayout>
        <Switch>
          <Route exact path={homeLink} component={HomePage} />
          <Route exact path={watcherLink} component={Watcher} />
          <Route exact path={explorerLink} component={Explorer} />
          <Route exact path={serviceLink} component={ServiceLevelIndicator} />
          <Route exact path={warningLink} component={Warning} />
          <Route exact path={ChangelogLink} component={Changelog} />
          <Route component={NotFound} />
          {children}
        </Switch>
      </MainLayout>
    </HashRouter>
  );
};

export default Router;
