import React, { PureComponent } from "react";
import logger from "../../../../../../lib/logger";
import UsdtLogo from "../../../../../../img/UsdtLogo.svg";
import { LinkButtonWithLogo } from "../LinkButtonWithLogo";

const refreshPriceTime = 8000;

const buildGetPriceUrl = (contractAddress) => {
  const url = `https://bsc.api.0x.org/swap/v1/price?sellToken=${contractAddress}&buyToken=DAI&sellAmount=1000000000`;
  return url;
};

export class TokenPriceIndicator extends PureComponent {
  constructor(props) {
    super(props);

    this.getPriceUrl = buildGetPriceUrl(props.contractAddress);
    this.chartUrl = `https://poocoin.app/tokens/${props.contractAddress}`;
    this.refreshInterval = setInterval(this.refreshPrice, refreshPriceTime);

    this.state = {
      price: null,
      error: false,
    };
  }

  componentDidMount() {
    this.refreshPrice();
  }

  componentWillUnmount() {
    clearInterval(this.refreshInterval);
  }

  refreshPrice = () => {
    fetch(this.getPriceUrl)
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
        throw new Error(res.error);
      })
      .then((res) => {
        if (res.price) {
          let newPrice = Number(res.price);
          newPrice = newPrice.toFixed(9); // TODO: Use decimals const from contract

          this.setState({ price: newPrice });
        }
      })
      .catch((error) => {
        this.setState({ error: true });
        logger.error(error);
        clearInterval(this.refreshInterval);
      });
  };

  render() {
    const { price, error } = this.state;

    let priceLabel = "";
    if (error) {
      priceLabel = " Could not Calculate (Careful!) ";
    } else {
      priceLabel = `Price: ${price ? `$ ${price}` : "Fetching..."}`;
    }

    return (
      <LinkButtonWithLogo
        href={this.chartUrl}
        logo={UsdtLogo}
        isCaution={!!error}
        isSuccess={!!price}
      >
        {priceLabel}
      </LinkButtonWithLogo>
    );
  }
}
