import React from "react";
import { Grid, Paper, Chip } from "@mui/material";
import { makeStyles } from "@material-ui/core/styles";
import { TokenCardSymbol } from "./TokenCardSymbol";
import { TokenCardName } from "./TokenCardName";
import { TokenCardLevel } from "./TokenCardLevel";
import { TokenCardLevelDescription } from "./TokenCardLevelDescription";
import { Spacer } from "./Spacer";
import { TokenCardChartButton } from "./TokenCardChartButton";
import { TokenCardViewMoreButton } from "./TokenCardViewMoreButton";
import { TokenCardBuyAtPancakeButton } from "./TokenCardBuyAtPancakeButton";
import { TokenCardBuyAtPoocoinButton } from "./TokenCardBuyAtPoocoinButton";
import { TokenAssessmentPopupPage } from "./TokenAssessmentPopupPage";
import Stack from "@mui/material/Stack";

const useStyles = makeStyles((theme) => ({
  tokenCardPaper: {
    width: "100%",
    borderRadius: "20px !important",
    background:
      "linear-gradient(180deg, rgba(165,216,255,1) 0%, rgba(58,122,190,1) 35%, rgba(20,91,167,1) 100%);",
    paddingTop: "35px",
    paddingLeft: "10px",
    paddingRight: "10px",
    paddingBottom: "35px",
  },
  spoonSafeLevelBackground: {
    // background: "linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab)",
    background:
      "linear-gradient(-45deg, #9400D3, #4B0082, #0000FF, #00FF00, #FFFF00, #FF7F00, #FF0000)",
    backgroundSize: "400% 400%",
    animation: "$spoonSafeLevelBackgroundAnimation 2s ease infinite",
  },
  "@keyframes spoonSafeLevelBackgroundAnimation": {
    "0%": {
      backgroundPosition: "0% 50%",
    },
    "50%": {
      backgroundPosition: "100% 50%",
    },
    "100%": {
      backgroundPosition: "0% 50%",
    },
  },
}));

export const WatcherTokenCard = ({ key, token }) => {
  const [tokenAssessmentOpen, setTokenAssessmentOpen] = React.useState(false);
  const [isNewEvent, setIsNewEvent] = React.useState(true);
  const classes = useStyles();

  const openTokenAssessmentPopup = () => {
    setTokenAssessmentOpen(true);
    setIsNewEvent(false);
  };

  const specialBackgroundClass =
    token.riskLevel.score === 4 ? classes.spoonSafeLevelBackground : null;

  return (
    <Grid item xs={12} lg={4}>
      <TokenAssessmentPopupPage
        popupOpen={tokenAssessmentOpen}
        setPopupOpen={setTokenAssessmentOpen}
        token={token}
      />
      <Paper
        className={`${classes.tokenCardPaper} ${specialBackgroundClass}`}
        key={key}
        elevation={24}
      >
        <TokenCardSymbol highlight={isNewEvent}>{token.symbol}</TokenCardSymbol>
        <TokenCardName>{token.name}</TokenCardName>
        <Spacer height="20" />
        <TokenCardLevel>{token.riskLevel.score + 1}</TokenCardLevel>
        <TokenCardLevelDescription>
          {token.riskLevel.label}
        </TokenCardLevelDescription>
        <Spacer height="40" />
        <Grid container xs={12}>
          <Grid item xs={12} padding={1}>
            <TokenCardViewMoreButton
              onClick={openTokenAssessmentPopup}
              contractAddress={token.contractAddress}
            />
          </Grid>
        </Grid>
        <Grid container xs={12}>
          <Grid item xs={12} sm={4} padding={1}>
            <TokenCardChartButton contractAddress={token.contractAddress} />
          </Grid>
          <Grid item xs={12} sm={4} padding={1}>
            <TokenCardBuyAtPancakeButton
              contractAddress={token.contractAddress}
            />
          </Grid>
          <Grid item xs={12} sm={4} padding={1}>
            <TokenCardBuyAtPoocoinButton
              contractAddress={token.contractAddress}
            />
          </Grid>
        </Grid>
      </Paper>
    </Grid>
  );
};
