import React, { useState, useMemo  } from 'react';
import useSound from 'use-sound';
import { GlobalState } from "../../lib/global";
import Pirun from '../../snd/Pirun.mp3';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import VolumeOffIcon from '@material-ui/icons/VolumeOff';

export const SoundRizer = () => {
  const [on, setOn] = useState(true)
  const [playSound, setPlaySound] = useState(false)
  const [constructorHasRun, setConstructorHasRun] = useState(false);

  const [play, { stop }] = useSound(
    Pirun,
    { volume: .25 }
  );

  const player = () => {
    if(on && playSound) {
      stop()
      play()
      setPlaySound(false)
    } else if(!on && playSound) {
      setPlaySound(false)
    }
  }
  
  const constructor = (constructorHasRun) => {
    if (constructorHasRun) return;
    setConstructorHasRun(true);
    GlobalState.Observer.onSoundNotification(() => setPlaySound(true))
  };

  useMemo(() => constructor(constructorHasRun), [constructorHasRun]);

  player()

  return (
    <>
      {on ? (
        <VolumeUpIcon onClick={() => {
          stop()
          setOn(false)
          setPlaySound(false)
        }} />
      ) : (
        <VolumeOffIcon onClick={() => setOn(true)} />
      )}
    </>
  );
}