import { EventType } from '../../models'

const LIQUIDITY_BURNED_EVENT_TYPE_ID = 'liquidity_burned'

const liquidityBurnedEventType = new EventType(
  LIQUIDITY_BURNED_EVENT_TYPE_ID,
  'Liquidity Burned',
)

const lPBurnTip =
  ' A piece or the total of the liquidity provided for this token has been burned. This means the LP Tokens have been transferred to a dead wallet that no one controls. This is a very good sign for a token but it does not mean it is entirely safe to trade by itself. It only means that a part of the liquidity cannot be rugged.'

export { LIQUIDITY_BURNED_EVENT_TYPE_ID, liquidityBurnedEventType, lPBurnTip }
