import { EventEmitter } from 'events'
import { GlobalState } from '../global'

export class Observer {
  #eventEmitter
  #TOKEN_EVENT_KEY
  #OPEN_SNACKBAR
  #onOpenSnackbarIsSet
  #SOUND_NOTIFICATION

  constructor() {
    this.#eventEmitter = new EventEmitter()
    this.#TOKEN_EVENT_KEY = 'token_event'
    this.#OPEN_SNACKBAR = 'open_snackbar'
    this.#SOUND_NOTIFICATION = 'sound_notification'
    this.#onOpenSnackbarIsSet = false
    this.#listen()
  }

  emitTokenEvent = (
    address,
    LPAddress,
    symbol,
    name,
    isMintable,
    transactionHash,
    blockNumber,
    eventType,
  ) => {
    const tokenEvent = {
      address,
      LPAddress,
      symbol,
      name,
      isMintable,
      transactionHash,
      blockNumber,
      eventType,
    }

    this.#eventEmitter.emit(this.#TOKEN_EVENT_KEY, tokenEvent)
  }

  emitOpenSnackbar = (message, variant = 'info', autoHideDuration = 3000) =>
    this.#eventEmitter.emit(
      this.#OPEN_SNACKBAR,
      message,
      variant,
      autoHideDuration,
    )

  onOpenSnackbar = (callback) => {
    if (this.#onOpenSnackbarIsSet) return

    this.#onOpenSnackbarIsSet = true
    this.#eventEmitter.on(
      this.#OPEN_SNACKBAR,
      (message, variant, autoHideDuration) => {
        callback(message, variant, autoHideDuration)
      },
    )
  }

  #onTokenEvent = () => {
    this.#eventEmitter.on(this.#TOKEN_EVENT_KEY, (token) => {
      const {
        address,
        LPAddress,
        symbol,
        name,
        isMintable,
        transactionHash,
        blockNumber,
        eventType,
      } = token
      GlobalState.TokenHandler.create(
        address,
        LPAddress,
        symbol,
        name,
        isMintable,
        transactionHash,
        blockNumber,
        eventType,
      )
    })
  }

  emitSoundNotification = () => this.#eventEmitter.emit(this.#SOUND_NOTIFICATION)

  onSoundNotification = (callback) => {
    this.#eventEmitter.on(this.#SOUND_NOTIFICATION, () => {
      callback()
    })
  }

  #listen = () => {
    this.#onTokenEvent()
  }
}
