import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import Link from "@material-ui/core/Link";
import AssignmentIcon from "@material-ui/icons/Assignment";
import RecentActors from "@material-ui/icons/RecentActors";
import { Trade } from "../../../common/Trade";
import { TableCellLink } from "../../../common/Table";
import { Event } from "../../../common/Event";
import PoocoinLogo from "../../../../img/PoocoinLogo.svg";
import PancakeLogo from "../../../../img/PancakeLogo.svg";
import TooltipToken from "../../../common/TooltipToken";
import {
  LIQUIDITY_CREATION_EVENT_TYPE_ID,
  LIQUIDITY_BURNED_EVENT_TYPE_ID,
} from "../../../../lib/constants";

const useStyles = makeStyles({
  logos: {
    width: 25,
    height: 25,
    marginRight: 15,
  },
});

const TableEvents = (props) => {
  const classes = useStyles();
  const { tokens, singleTokenMode } = props;
  return (
    <TableBody>
      {tokens.map((token) =>
        token.events.map((event, index) => (
          <TableRow key={`Explorer_${token.symbol}_${index}`}>
            <TableCell>{event.time}</TableCell>
            <TableCell>
              <Link
                href={`https://bscscan.com/block/${event.blockNumber}`}
                target="_blank"
                rel="noopener"
                color="textSecondary"
              >
                {event.blockNumber}
              </Link>
            </TableCell>
            {!singleTokenMode && (
              <TableCellLink
                href={`https://bscscan.com/address/${token.contractAddress}`}
              >
                {token.symbol} - {token.name}
              </TableCellLink>
            )}
            {!singleTokenMode && (
              <TableCellLink
                href={`https://bscscan.com/address/${token.contractAddress}#code`}
              >
                <AssignmentIcon />
              </TableCellLink>
            )}
            {event.eventType.identifier === LIQUIDITY_BURNED_EVENT_TYPE_ID ||
            event.eventType.identifier === LIQUIDITY_CREATION_EVENT_TYPE_ID
              ? !singleTokenMode && (
                  <TableCellLink
                    href={`https://bscscan.com/token/${token.LPAddress}#balances`}
                  >
                    <RecentActors />
                  </TableCellLink>
                )
              : !singleTokenMode && <TableCell></TableCell>}
            <TableCell>
              <Event eventType={event.eventType} />
            </TableCell>
            <TableCell>
              <TooltipToken eventType={event.eventType} placement="right" />
            </TableCell>
            <TableCell>
              <Link
                href={`https://bscscan.com/tx/${event.txHash}`}
                target="_blank"
                rel="noopener"
                color="textSecondary"
              >
                {event.txHash}
              </Link>
            </TableCell>
            {!singleTokenMode && (
              <TableCell>
                <Trade
                  href={`https://poocoin.app/tokens/${token.contractAddress}`}
                  img={PoocoinLogo}
                  className={classes.logos}
                  alt="Poocoin"
                />
                <Trade
                  href={`https://pancakeswap.finance/swap?outputCurrency=${token.contractAddress}`}
                  img={PancakeLogo}
                  className={classes.logos}
                  alt="Pancake"
                />
              </TableCell>
            )}
          </TableRow>
        ))
      )}
    </TableBody>
  );
};

export default TableEvents;
