import React from "react";
import { Typography } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import DropDown from "../../common/DropDown";
import RemoveRedEyeIcon from "@material-ui/icons/RemoveRedEye";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import AssessmentIcon from "@material-ui/icons/Assessment";
import MonetizationOnIcon from "@material-ui/icons/MonetizationOn";
import Home from "@material-ui/icons/Home";
import AssignmentIcon from "@material-ui/icons/Assignment";
import WebIcon from "@material-ui/icons/Web";
import NotificationsActiveIcon from "@material-ui/icons/NotificationsActive";
import WarningIcon from "@material-ui/icons/Warning";
import Box from "@material-ui/core/Box";

export const Alpha11 = (props) => {
  const className = props.className;
  return (
    <Grid className={className}>
      <DropDown name="SPOON ALPHA 0.1.1">
        <Typography>
          <Box>
            <AssessmentIcon /> - New mint function risk assessment
          </Box>
          <Box>
            <Home /> - New home page (eventually will hold media)
          </Box>
          <Box>
            <AssignmentIcon /> - Added quick contract verification link for
            tokens in SPOON Watcher
          </Box>
          <Box>
            <ZoomInIcon /> - Improved navigation and performance of SPOON
            Explorer
          </Box>
          <Box>
            <MonetizationOnIcon /> - Added live spoon price indicator
          </Box>
          <Box>
            <WebIcon /> - Overall UI and UX improvements
          </Box>
          <Box>
            <NotificationsActiveIcon /> - New notification when a token has been
            added to SPOON Watcher
          </Box>
          <Box>
            <RemoveRedEyeIcon /> - New risk filter in SPOON Watcher (we
            recommend you skip Very Risky investments)
          </Box>
          <Box>
            <WarningIcon /> - New warning indications before trading in an
            investment.
          </Box>
        </Typography>
      </DropDown>
    </Grid>
  );
};
