import TokenSnifferLogo from '../../img/TokenSnifferLogo.png';
import PoocoinLogo from '../../img/PoocoinLogo.svg';
import BitqueryLogo from '../../img/BitQueryLogo.png';
import HoneypotDetector from '../../img/HoneypotDetector.png';
import BscScanLogo from '../../img/BscScanLogo.svg';

class ThirdPartyAppDefinition {
  constructor(iconRelativeUrl, appTitle, appDescription, buildLinkFunction) {
    this.iconRelativeUrl = iconRelativeUrl;
    this.appTitle = appTitle;
    this.appDescription = appDescription;
    this.buildLinkFunction = buildLinkFunction;
  }
}

const tokenSnifferDef = new ThirdPartyAppDefinition(
  TokenSnifferLogo,
  "Token Sniffer",
  "Token Sniffer provides analysis on the token about how it swaps, what the contract is like, who are the holders, how is the liquidity armed and much more. A must-stop for any investor.",
  (contractAddress, network) =>
    `https://tokensniffer.com/token/${contractAddress}`
);

const poocoinDevWalletChecker = new ThirdPartyAppDefinition(
  PoocoinLogo,
  "Poocoin Dev Wallet",
  "Use the Dev Wallet Checker at Poocoin to better understand what the devs are doing with the tokens. This could help you identify hidden wallets, hidden transactions or chances of a rug pull.",
  (contractAddress, network) =>
    `https://poocoin.app/rugcheck/${contractAddress}/dev-activity`
);

const poocoinTopHoldersChecker = new ThirdPartyAppDefinition(
  PoocoinLogo,
  "Poocoin Top Holders",
  "It is always useful to understand who the holders of a token are. If you see something that is not supposed to have a lot of tokens then you are in for a rug pull.",
  (contractAddress, network) =>
    `https://poocoin.app/rugcheck/${contractAddress}/top-holders`
);

const bitqueryExplorer = new ThirdPartyAppDefinition(
  BitqueryLogo,
  "Bitquery Explorer",
  "Use the explorer from Bitquery to understand the trading volume of a token, where it trades, the internals of the smart contract and more.",
  (contractAddress, network) =>
    `https://explorer.bitquery.io/bsc/token/${contractAddress}`
);

const honeypotDetector = new ThirdPartyAppDefinition(
  HoneypotDetector,
  "Honeypot Detector",
  "Great tool for detecting honeypots by simulating buy and sell operations without costing you a single dime! The honeypot detector works with any token that has liquidity with BNB.",
  (contractAddress, network) => `https://honeypot.is/?address=${contractAddress}`
)

const bscscanTokenChart = new ThirdPartyAppDefinition(
  BscScanLogo,
  "BscScan Holders Chart",
  "Analyse and understand who are the top holders of the token and compare the data to decide whether it is safe to invest or not.",
  (contractAddress, network) => `https://bscscan.com/token/tokenholderchart/${contractAddress}`
)

// Continue adding from https://poocoin.app/external-tools

export const ThirdPartyAppDefinitions = [
  tokenSnifferDef,
  poocoinDevWalletChecker,
  poocoinTopHoldersChecker,
  bitqueryExplorer,
  honeypotDetector,
  bscscanTokenChart
];
