export class CakeLPContract {
  constructor(contract) {
    this.contract = contract

    this.token0Address = null
    this.token1Address = null
  }

  async isCakeLPContract() {
    try {
      await this.getToken0Address()
      await this.getToken1Address()

      return true
    } catch (ex) {
      return false
    }
  }

  async getTokenAddress(tokenNumber) {
    const field = `token${tokenNumber}Address`
    const method = `token${tokenNumber}`

    if (!this[field]) {
      this[field] = await this.contract.methods[method]().call()
    }

    return this[field]
  }

  async getToken0Address() {
    return await this.getTokenAddress('0')
  }

  async getToken1Address() {
    return await this.getTokenAddress('1')
  }
}
