import React from "react";
import HoldersIcon from "../../../../../../img/HoldersIcon.svg";
import { LinkButtonWithLogo } from "../LinkButtonWithLogo";

export const CheckTokenHoldersButton = ({
  contractAddress,
  alreadyClicked,
  onClick,
}) => {
  const url = `https://bscscan.com/token/${contractAddress}#balances`;
  const text = "Check Token Holders";

  return (
    <LinkButtonWithLogo
      onClick={onClick}
      href={url}
      logo={HoldersIcon}
      alt={text}
      isWarning={!alreadyClicked}
    >
      {text}
    </LinkButtonWithLogo>
  );
};
