import React from "react";
import Grid from "@material-ui/core/Grid";
import { autocompleteClasses } from "@mui/material";
import { WatcherTokenCard } from "./WatcherTokenCard";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles(() => ({
  gridContainer: {
    marginLeft: "auto",
    marginRight: "auto",
  },
}));

const WatcherGrid = (props) => {
  const { tokens, tokenSelected, onOpenCard } = props;
  const classes = useStyles();

  return (
    <Grid
      container
      direction="row"
      justifyContent="center"
      flexDirection="row-reverse"
      alignItems="center"
      className={classes.gridContainer}
      xs={12}
      lg={10}
      xl={8}
      spacing={2}
    >
      {tokens.map((token, index) => (
        <WatcherTokenCard key={index} token={token} />
      ))}
    </Grid>
  );
};

export default WatcherGrid;
