import { Button } from "@material-ui/core";
import { useTheme, makeStyles } from "@material-ui/core";
import { green, red, yellow } from "@material-ui/core/colors";

const useStyles = makeStyles({
  buttonLogo: {
    cursor: "pointer",
    width: "26px",
    height: "26px",
    lineHeight: "24px",
    marginRight: "5px",
  },
  buttonText: {
    height: "26px",
  },
  "@keyframes linkButtonWithLogoHover": {
    to: {
      backgroundPosition: "20vw",
    },
  },
  button: {
    cursor: "pointer",
    "&:hover": {
      backgroundImage:
        "linear-gradient(90deg, #00C0FF 0%, #FFCF00 49%, #FC4F4F 80%, #00C0FF 100%)",
      animation: "$linkButtonWithLogoHover 5s linear infinite",
    },
  },
});

export const LinkButtonWithLogo = ({
  onClick,
  href,
  logo,
  children,
  alt,
  buttonStyle = {},
  isWarning,
  isCaution,
  isSuccess,
}) => {
  const theme = useTheme();
  const classes = useStyles();

  let backgroundColor = theme.palette.text.secondary;
  if (isWarning) backgroundColor = yellow.A400;
  if (isCaution) backgroundColor = red["50"];
  if (isSuccess) backgroundColor = green.A200;

  return (
    <Button
      onClick={onClick}
      href={href}
      target="_blank"
      rel="noopener"
      className={classes.button}
      style={{
        fontSize: "15px",
        width: "100%",
        color: theme.palette.primary.main,
        backgroundColor,
        fontWeight: "bold",
        ...buttonStyle,
      }}
      variant="contained"
      color="textSecondary"
    >
      <img src={logo} className={classes.buttonLogo} alt={children} />
      <div className={classes.buttonText}>
        <div>{children}</div>
      </div>
    </Button>
  );
};
