import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import Copyright from "./Copyright";

const useStyles = makeStyles((theme) => ({
  footer: {
    padding: theme.spacing(2, 0, 2, 0),
    marginTop: "auto",
    backgroundColor: theme.palette.footer.background,
  },
  alignFooter: {
    textAlign: "center",
  },
}));

const Footer = () => {
  const classes = useStyles();
  return (
    <footer className={classes.footer}>
      <Container>
        Disclaimer: The information provided shall not in any way constitute a
        recommendation as to whether you should invest in any product analyzed
        by this app.
        <div>
          &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;
          We accept no liability for any loss occasioned to any person acting or
          refraining from action as a result of any material provided.
        </div>
      </Container>
      <Container maxWidth="sm" className={classes.alignFooter}>
        <Copyright />
      </Container>
    </footer>
  );
};

export default Footer;
