import { RiskLevel } from "../models";
import {
  LIQUIDITY_BURNED_EVENT_TYPE_ID,
  OWNERSHIP_RENOUNCED_EVENT_TYPE_ID,
  mintHints,
} from "../constants";
import { GlobalState } from "../global/GlobalState";

export const RiskLevelAnalyser = {
  runAssessment: function (token) {
    const riskAnalysisDescriptionArray = [];

    if (!token.events.length) {
      new RiskLevel(0); // By default it is very risky
    } else {
      riskAnalysisDescriptionArray.push(
        ` ${mintHints[token.isMintable ? 0 : 1]}`
      );
      riskAnalysisDescriptionArray.push(
        " Liquidity was created at some point in the blockchain."
      );
    }

    let safetyLevel = 0;

    // const hasOwnershipRenounced = token.events.some(
    //   (x) => x.eventType.identifier === OWNERSHIP_RENOUNCED_EVENT_TYPE_ID,
    // )
    // if (hasOwnershipRenounced) {
    //   safetyLevel++
    //   riskAnalysisDescriptionArray.push(
    //     ' Contract Ownership has been renounced.',
    //   )
    // } else {
    //   riskAnalysisDescriptionArray.push(
    //     ' Contract Ownership is not renounced. This is not a bad indicator per se.',
    //   )
    // }

    const hasLiqBurned = token.events.some(
      (evt) => evt.eventType.identifier === LIQUIDITY_BURNED_EVENT_TYPE_ID
    );
    if (hasLiqBurned) {
      safetyLevel++;
      riskAnalysisDescriptionArray.push(
        " Liquidity has been burned to the dead wallet address. Ensure the percentage of liquidity burned is considerable."
      );
    } else {
      riskAnalysisDescriptionArray.push(
        " Liquidity is not burned. Check if the team has locked the liquidity and for how long. Do not operate with any token whose liquidity is not locked or burned."
      );
      riskAnalysisDescriptionArray.push(
        " The SPOON App will detect all types of liquidity locks in the future, as well as their duration."
      );
    }

    const isWhitelisted =
      !!GlobalState.WhitelistedAddresses[token.contractAddress.toUpperCase()];
    if (isWhitelisted) {
      riskAnalysisDescriptionArray.push(
        " Project has been whitelisted by our risk analysis team. Should be safe to invest in."
      );
      return [new RiskLevel(4), riskAnalysisDescriptionArray];
    }

    return [new RiskLevel(safetyLevel), riskAnalysisDescriptionArray];
  },
};
