import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles(() => ({
  tokenCardLevelDescriptionContainer: {
    marginLeft: "auto",
    marginRight: "auto",
    textAlign: "center",
  },
  tokenCardLevelDescriptionText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
}));

export const TokenCardLevelDescription = ({ children }) => {
  const classes = useStyles();

  return (
    <div className={classes.tokenCardLevelDescriptionContainer}>
      <Typography className={classes.tokenCardLevelDescriptionText}>
        {children}
      </Typography>
    </div>
  );
};
