import React from "react";
import { TradeLink } from './TradeLink';
import { TradeButton } from './TradeButton';
import { DYORConsumer } from "../../../lib/dyor";

export const Trade = (props) => {
  const { href, img, alt, title } = props;
  return (
    <DYORConsumer>
      {(value) => {
        const { dontShowAgain, handleDYOR } = value;
        return (
          <>
            {dontShowAgain ? <TradeLink
              href={href}
              img={img}
              className={props.className}
              alt={alt}
              title={title}
            /> : <TradeButton
              href={href}
              img={img}
              className={props.className}
              alt={alt}
              handleDYOR={handleDYOR}
              title={title}
            />}
          </>
        );
      }}
    </DYORConsumer>
  );
};