import { GlobalState } from '../global'
import { newTokenEventType } from '../constants'
import logger from '../logger'

export class NewTokenHunter {
  isTransactionTargetable(pkg) {
    // Have no "to" property means it is a new token
    return !pkg.transaction.to
  }

  async isEventConfirmed(pkg) {
    await pkg.ensureAsTokenContract()

    return await pkg.contract.isTokenContract()
  }

  async present(pkg) {
    await pkg.ensureAsTokenContract()

    const description = await pkg.contract.getDescription()
    const isMintable = await pkg.contract.isMintable()

    GlobalState.Observer.emitTokenEvent(
      pkg.receipt.contractAddress,
      null,
      pkg.contract.symbol,
      pkg.contract.name,
      isMintable,
      pkg.transaction.hash,
      pkg.receipt.blockNumber,
      newTokenEventType,
    )

    GlobalState.showExplorerEvent(pkg.contract.symbol, 'New Token')

    logger.info({
      evt: `NewToken - ${description}`,
      hash: pkg.transaction.hash,
      tx: pkg.transaction,
    })
  }
}
