import { buildAsTokenContract, buildAsCakeLpContract } from '../operators'
import { NullContract } from '../blockchain'
import logger from "../logger"

export class TransactionPackage {
  connector = null
  transaction = null
  contract = null
  receipt = null

  constructor(connector, transaction) {
    this.connector = connector
    this.transaction = transaction
  }

  async ensureReceipt() {
    try {
      this.receipt =
        this.receipt ||
        (await this.connector.getTransactionReceipt(this.transaction.hash))
    } catch (ex) {
      logger.error(ex)
    }
  }

  async ensureAsTokenContract() {
    await this.ensureReceipt()

    if (!this.receipt || !this.receipt.contractAddress) {
      this.contract = new NullContract()
    }

    try {
      this.contract =
        this.contract ||
        (await buildAsTokenContract(
          this.connector,
          this.receipt.contractAddress,
        ))
    } catch (ex) {
      logger.error(ex)
    }
  }

  async ensureAsCakeLpContract(logIndex) {
    await this.ensureReceipt()

    if (
      !this.receipt ||
      !this.receipt.logs ||
      !this.receipt.logs[logIndex] ||
      !this.receipt.logs[logIndex].address
    ) {
      this.contract = new NullContract()
    }

    try {
      this.contract =
        this.contract ||
        (await buildAsCakeLpContract(
          this.connector,
          this.receipt.logs[logIndex].address,
        ))
    } catch (ex) {
      logger.error(ex)
    }
  }
}
