import React from "react";
import BscScanLogo from "../../../../../../img/BscScanLogo.svg";
import { LinkButtonWithLogo } from "../LinkButtonWithLogo";

const buttonStyle = {
  justifyContent: "flex-start",
  textTransform: "none",
};

export const ContractAddressButton = ({ contractAddress }) => {
  const url = `https://bscscan.com/address/${contractAddress}`;

  return (
    <LinkButtonWithLogo
      href={url}
      logo={BscScanLogo}
      alt={contractAddress}
      buttonStyle={buttonStyle}
    >
      {`Address: ${contractAddress}`}
    </LinkButtonWithLogo>
  );
};
