import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { TokenAssessmentContentContainer } from "../TokenAssessmentContentContainer";
import { ThirdPartyAppCard } from "./ThirdPartyAppCard";

const explanationText =
  "Use the following additional tools from third party websites to quickly assess additional information and to also get a second opinion on the risk level of this token. Always do your own research before investing.";

export const ThirdPartyAppsTab = ({ contractAddress, appDefinitions = [] }) => {
  return (
    <TokenAssessmentContentContainer>
      <Typography variant="h6">{explanationText}</Typography>
      <Grid container xs={12}>
        {appDefinitions.length &&
          appDefinitions.map((def) => (
            <Grid item xs={4}>
              <ThirdPartyAppCard
                key={def.appTitle}
                appDefinition={def}
                contractAddress={contractAddress}
              />
            </Grid>
          ))}
      </Grid>
    </TokenAssessmentContentContainer>
  );
};
