import React from 'react';
import { Link } from 'react-router-dom';

export const LinkRouter = (props) => {
    return (
        <Link to={props.to} style={{ color: props.color == null ? 'inherit' : props.color,
                                     textDecoration: props.textDecoration == null ? 'inherit' : props.textDecoration}}
                                     className={props.className}>
            {props.children}
        </Link>
    );
}