import { TransactionPackage } from '../eventHunter'
import { delay } from '../operators'
import logger from '../logger'
import { GlobalState } from '../global/GlobalState'

export class Voyager {
  constructor(
    connectorRotator,
    defaultConnector,
    eventHunters,
    startingBlock = null,
    endingBlock = null,
  ) {
    this.connectorRotator = connectorRotator
    this.defaultConnector = defaultConnector
    this.blockNumber = 0
    this.currentBlock = -1
    this.eventHunters = eventHunters
    this.startingBlock = startingBlock
    this.endingBlock = endingBlock

    this.startedBlock = this.startingBlock || null
  }

  async run(context) {
    if (!context) {
      context = this
      logger.info('Voyager Engine has started.')
    }

    const { defaultConnector, processBlock } = context

    const latestBlock =
      !!context.startingBlock && context.currentBlock === -1
        ? context.startingBlock
        : await defaultConnector.getBlockNumber()

    if (!this.startedBlock) this.startedBlock = latestBlock

    while (latestBlock >= context.currentBlock) {
      if (context.currentBlock === -1) context.currentBlock = latestBlock

      // Cancel all voyage if we have reached the ending block
      // which is only used for debugging purposes
      if (!!context.endingBlock && context.currentBlock > context.endingBlock)
        return

      // Inform current mining status
      GlobalState.AppComponent.setBlocksExecuted({
        startBlock: context.startedBlock,
        currentBlock: context.currentBlock,
        latestBlock,
      })

      logger.info(`${context.currentBlock} / ${latestBlock}`)

      await processBlock(context)

      context.currentBlock++

      await delay(1000)
    }

    setTimeout(async () => await context.run(context), 1000)
  }

  async processBlock(context) {
    const { defaultConnector, currentBlock } = context

    // Get the information about this block and its transactions (unfortunately without receipts)
    let block = await defaultConnector.getBlockWithTransactions(
      currentBlock,
      true,
    )

    if (!block || !block.transactions) {
      block = await defaultConnector.getBlockWithTransactions(
        currentBlock,
        true,
      )
    }

    if (!block || !block.transactions) {
      console.error({ msg: 'Could not get transactions for this block', block })
      return
    }

    // We will be running a lot of promises in parallel for maximum performance
    const promises = []

    const curry = (transaction, context) => async () => {
      await context.processTransaction(transaction, context)
    }

    for (let i = 0; i < block.transactions.length; i++) {
      const transaction = block.transactions[i]

      // We should process transactions in parallel but wait until all are completed
      const closure = curry(transaction, context)
      promises.push(closure())
    }

    await Promise.all(promises)
  }

  async processTransaction(transaction, context) {
    const { connectorRotator, eventHunters } = context

    // Get the specified connector for our tx from the rotator
    const connector = connectorRotator(transaction.hash)

    // Build the transaction package
    const pkg = new TransactionPackage(connector, transaction)

    // See if any of the event hunters think its targettable
    const candidateEventHunters = eventHunters.filter((evtHunter) =>
      evtHunter.isTransactionTargetable(pkg),
    )

    // Leave if there are no interested hunters
    if (!candidateEventHunters.length) return

    // Go each one by one (blockchain requests are made one by one)
    for (let i = 0; i < candidateEventHunters.length; i++) {
      const evtHunter = candidateEventHunters[i]

      const isConfirmed = await evtHunter.isEventConfirmed(pkg)

      if (isConfirmed) {
        await evtHunter.present(pkg)
        break
      }
    }
  }
}
