import { Box } from "@material-ui/core";

export const TokenAssessmentTabPanel = (props) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`token-assessment-tabpanel-${index}`}
      aria-labelledby={`token-assessment-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ pt: 3, pb: 3 }}>{children}</Box>}
    </div>
  );
};
