import { useTheme } from "@material-ui/core";
import { LinkButtonWithLogo } from "./LinkButtonWithLogo";
import ChartIcon from "../../../../../img/ChartIcon.svg";

export const TokenCardChartButton = ({ contractAddress }) => {
  const href = `https://poocoin.app/tokens/${contractAddress}`;
  const theme = useTheme();

  return (
    <LinkButtonWithLogo href={href} logo={ChartIcon}>
      Chart
    </LinkButtonWithLogo>
  );
};
