import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Typography, Alert, AlertTitle } from "@mui/material";
import { withTheme } from "@material-ui/styles";
import Multiselect from "multiselect-react-dropdown";
import { riskLabels } from "../../../lib/constants";

const useStyles = makeStyles(() => ({
  filterContainer: {},
}));

const FilterPanelComponent = ({ setFilters, filters, theme }) => {
  const classes = useStyles();

  const multiSelectStyle = {
    multiselectContainer: {
      height: "30px",
      width: "100%",
    },
    optionContainer: {
      backgroundColor: theme.palette.primary.main,
    },
  };

  const options = riskLabels.map((riskLabel, index) => {
    return { id: index, value: riskLabel };
  });

  return (
    <>
      <Typography variant="h5" component="h5" marginBottom="10px">
        FILTERS
      </Typography>
      <Typography marginBottom="10px">
        <Alert severity="warning">
          <AlertTitle>
            <strong>Disclaimer</strong>
          </AlertTitle>
          Levels SPOON 4 and SPOON 5 cannot be obtained yet. SPOON is under
          development and further risk assessment features are needed in order
          to catalog a token as safer.
        </Alert>
      </Typography>
      <div className={classes.filterContainer}>
        <Multiselect
          marginBottom="20px"
          placeholder="Select Risk Levels"
          displayValue="value"
          closeOnSelect={false}
          isObject
          showCheckbox
          avoidHighlightFirstOption
          options={options}
          selectedValues={filters}
          style={multiSelectStyle}
          onRemove={(selectedList) => {
            setFilters(selectedList);
          }}
          onSelect={(selectedList, selectedItem) => {
            setFilters(selectedList);
          }}
        />
      </div>
    </>
  );
};

export const FilterPanel = withTheme(FilterPanelComponent);
