import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Container,
  Card,
  CardHeader,
  Grid,
  Box,
  Button,
  CardActions,
  CardContent,
  Typography,
} from "@material-ui/core";
import StarIcon from "@material-ui/icons/StarBorder";
import { buySpoonUrl } from "../../../lib/constants";
import Alert from "@material-ui/lab/Alert";

const tiers = [
  {
    title: "Free",
    subheader: "Try a subset of features for free before buying SPOONs",
    price: "0",
    spoons: "0",
    spoonLps: "0",
    description: [
      " Basic toolset to try SPOON",
      " Live events feed",
      " Basic risk assessment",
      " Detection of new tokens, ownership renounced, liquidity provided and liquidity burned.",
      " Fav your investments",
      " Report token feature",
    ],
    buttonText: "Buy SPOONs now",
    buttonVariant: "contained",
    code: "free",
  },
  {
    title: "Pro Ape",
    subheader: "Safer investing through advanced risk assessment",
    price: "1500 SPOONs",
    spoons: "1500",
    spoonLps: "500",
    description: [
      " All of the Free tier features",
      " Verified contract detection",
      " Dev Wallet tokens assessment",
      " Liquidity risk assessment",
      " Initial tokens distribution assessment",
      " LP Holders risk assessment",
      " Liquidity lock events detection",
      " Liquidity lock risk assessment",
      " Hardcoded and excluded addresses assessment",
    ],
    buttonText: "Buy SPOONs now",
    buttonVariant: "contained",
    code: "ape",
  },
  {
    title: "Diamond Spooner",
    subheader: "Complete risk assessment for the advanced trader",
    price: "3000 SPOONs",
    spoons: "3000",
    spoonLps: "200",
    description: [
      " All of Free and Pro Ape features",
      " Sound alarms",
      " Browser notifications",
      " Fast trading operations",
      " Contract comments disclosure",
      " Fast contract navigation",
      " Quick transfer method check",
      " Quick approve method check",
      " Honeypot checks",
      " Rug pull risk assessment",
      " Slow rug pull risk asssessment",
      " Compare contracts to known scams",
      " Full fledged security audits",
      " Only Owner methods risk assessment",
      " Special modifiers risk assessment",
      " Min function risk assessment",
      " On-demand contract check",
    ],
    buttonText: "Buy SPOONs now",
    buttonVariant: "contained",
    code: "diamond",
  },
];

const useStyles = makeStyles((theme) => ({
  pricingUl: {
    margin: 0,
    padding: 0,
    listStyle: "none",
  },
  pricingContainer: {
    display: "flex",
    alignItems: "flex-start",
    flexWrap: "wrap",
  },
  pricingCardContainer: {
    padding: "20px",
  },
  priceTag: {
    marginRight: "5px",
  },
  priceCardHeader: {
    backgroundColor: theme.palette.primary.priceTag,
  },
  featureLine: {
    marginBottom: "10px",
  },
}));

const ServiceLevelIndicator = () => {
  const classes = useStyles();

  return (
    <React.Fragment>
      <Alert variant="filled" severity="info">
         Indicates a feature that is currently in development
      </Alert>
      <Container
        maxWidth="lg"
        className={classes.pricingContainer}
        component="main"
      >
        {tiers.map((tier) => (
          <Grid
            className={classes.pricingCardContainer}
            item
            key={tier.title}
            xs={12}
            sm={tier.code === "diamond" ? 12 : 6}
            md={4}
          >
            <Card>
              <CardHeader
                title={tier.title}
                subheader={tier.subheader}
                titleTypographyProps={{ align: "center" }}
                action={tier.code === "ape" ? <StarIcon /> : null}
                subheaderTypographyProps={{
                  align: "center",
                }}
                className={classes.priceCardHeader}
              />
              <CardActions>
                <Button
                  fullWidth
                  variant={tier.buttonVariant}
                  target="_blank"
                  rel="noopener"
                  href={buySpoonUrl}
                >
                  {tier.buttonText}
                </Button>
              </CardActions>
              <CardContent>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "baseline",
                    mb: 2,
                  }}
                >
                  <Typography
                    component="h2"
                    variant="h3"
                    color="text.primary"
                    className={classes.priceTag}
                  >
                    {tier.spoons}
                  </Typography>
                  <Typography variant="h6" color="text.secondary">
                    SPOONs
                  </Typography>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "baseline",
                  }}
                ></Box>
                <ul className={classes.pricingUl}>
                  {tier.description.map((line) => (
                    <Typography
                      component="li"
                      variant="subtitle1"
                      align="center"
                      key={line}
                      className={classes.featureLine}
                    >
                      {line}
                    </Typography>
                  ))}
                </ul>
              </CardContent>
              <CardActions>
                <Button
                  fullWidth
                  variant={tier.buttonVariant}
                  target="_blank"
                  rel="noopener"
                  href={buySpoonUrl}
                >
                  {tier.buttonText}
                </Button>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Container>
    </React.Fragment>
  );
};

export default ServiceLevelIndicator;
