import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { green } from "@material-ui/core/colors";

const useStyles = makeStyles(() => ({
  tokenCardSymbolContainer: {
    marginLeft: "auto",
    marginRight: "auto",
    textAlign: "center",
  },
  tokenCardSymbolText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
  },
  highlighted: {
    color: green["900"],
  },
}));

export const TokenCardSymbol = ({ children, highlight }) => {
  const classes = useStyles();

  return (
    <div
      className={`${classes.tokenCardSymbolContainer} ${
        highlight ? classes.highlighted : null
      }`}
    >
      <Typography className={classes.tokenCardSymbolText} variant="h3">
        {children}
      </Typography>
    </div>
  );
};
