import React, { useEffect, useState } from "react";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import { makeStyles } from "@material-ui/core/styles";
import { getPriceUrl, buySPOONUrl } from "../../lib/constants";
import logger from "../../lib/logger";

const useStyles = makeStyles(() => ({
  marginR: {
    marginRight: 15,
  },
  colors: {
    color: "#000000",
  },
}));

const Price = () => {
  const classes = useStyles();
  const [price, setItems] = useState();
  let priceFix;
  const [errorMsgSent, setMsg] = useState();

  useEffect(() => {
    const getPrice = () => {
      fetch(getPriceUrl)
        .then((res) => res.json())
        .then(
          (result) => {
            setItems(result.price);
          },
          (error) => {
            if (errorMsgSent !== error.message) {
              setMsg(error.message);
              logger.error(error.message);
            }
          }
        );
      setTimeout(getPrice, 5000);
    };

    getPrice();
  }, [errorMsgSent]);

  const priceIsSet = price !== undefined;

  if (priceIsSet) {
    priceFix = Number(price);
    priceFix = priceFix.toFixed(3);
  }

  return (
    <Typography className={classes.marginR}>
      <Link href={buySPOONUrl} target="_blank" className={classes.colors}>
        {!priceIsSet ? (
          <Typography> Price: -</Typography>
        ) : (
          <Typography> Price: $ {priceFix} </Typography>
        )}
      </Link>
    </Typography>
  );
};

export default Price;
