import React from "react";
import { Box, Tabs, Tab } from "@material-ui/core";
import { tokenAssessmentTips } from "../../../../../lib/constants";
import { ThirdPartyAppDefinitions } from "../../../../../lib/constants/thirdPartyAppDefinitions";
import { TokenAssessmentTabPanel } from "./TokenAssessmentTabPanel";
import { SummaryTab } from "./SummaryTab/SummaryTab";
import { WarningsTab } from "./WarningsTab/WarningsTab";
import { ThirdPartyAppsTab } from "./ThirdPartyAppsTab";
import { EventsTab } from "./EventsTab";

const a11yProps = (index) => ({
  id: `token-assessment-tab-${index}`,
  "aria-controls": `token-assessment-tab-${index}`,
});

export const TokenAssessmentContent = ({ token }) => {
  const [selectedTab, setSelectedTab] = React.useState(0);

  const handleSelectedTabChange = (event, newSelectedTab) => {
    setSelectedTab(newSelectedTab);
  };

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          value={selectedTab}
          onChange={handleSelectedTabChange}
          aria-label="token-assessment-tab"
        >
          <Tab label="Summary" {...a11yProps(0)} />
          <Tab label="Additional Tools" {...a11yProps(1)} />
          <Tab label="Events" {...a11yProps(2)} />
          <Tab label="Warnings" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TokenAssessmentTabPanel
        style={{ padding: "0px" }}
        value={selectedTab}
        index={0}
      >
        <SummaryTab token={token} />
      </TokenAssessmentTabPanel>
      <TokenAssessmentTabPanel value={selectedTab} index={1}>
        <ThirdPartyAppsTab
          contractAddress={token.contractAddress}
          appDefinitions={ThirdPartyAppDefinitions}
        />
      </TokenAssessmentTabPanel>
      <TokenAssessmentTabPanel value={selectedTab} index={2}>
        <EventsTab token={token} />
      </TokenAssessmentTabPanel>
      <TokenAssessmentTabPanel value={selectedTab} index={3}>
        <WarningsTab tips={tokenAssessmentTips} />
      </TokenAssessmentTabPanel>
    </Box>
  );
};
