import { Fade } from "@material-ui/core";
import { ModalBase } from "../../../../common/Modals/ModalBase";
import { TokenAssessmentPopupTemplate } from "./TokenAssessmentPopupTemplate";

export const TokenAssessmentPopupPage = ({
  popupOpen,
  setPopupOpen,
  token,
}) => {
  const handleClose = () => setPopupOpen(false);

  return (
    <ModalBase open={popupOpen} onClose={handleClose} width={"75%"}>
      <Fade in={popupOpen}>
        <TokenAssessmentPopupTemplate token={token} onClose={handleClose} />
      </Fade>
    </ModalBase>
  );
};
