import React from "react";
import { withRouter } from "react-router-dom";
import clsx from "clsx";
import { useTheme, makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import Drawer from "@material-ui/core/Drawer";
import { drawerWidth,homeHash,homeLink,watcherHash,watcherLink,explorerHash,explorerLink,serviceHash,serviceLink,
         warningHash,warningLink,ChangelogHash,ChangelogLink } from "../../../lib/constants";
import RemoveRedEyeIcon from "@material-ui/icons/RemoveRedEye";
import LocalOfferIcon from "@material-ui/icons/LocalOffer";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import DrawerTabs from "../../common/DrawerTabs";
import Home from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import { SoundRizer } from "../../common/SoundRizer";
import HistoryIcon from '@material-ui/icons/History';

const useStyles = makeStyles((theme) => ({
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
}));

const AppDrawer = (props) => {
  const { open, setOpen } = props;
  const classes = useStyles();
  const theme = useTheme();
  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Drawer
      variant="permanent"
      className={clsx(classes.drawer, {
        [classes.drawerOpen]: open,
        [classes.drawerClose]: !open,
      })}
      classes={{
        paper: clsx({
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        }),
      }}
    >
      <div className={classes.toolbar}>
        <IconButton onClick={handleDrawerClose}>
          {theme.direction === "rtl" ? (
            <ChevronRightIcon />
          ) : (
            <ChevronLeftIcon />
          )}
        </IconButton>
      </div>
      <Divider />
      <List>
        {["Home", "SPOON Explorer", "SPOON Watcher"].map((text, index) => (
          <div>
            {text === "Home" && (
              <DrawerTabs
                key={`Home_${index}`}
                to={homeLink}
                hash={homeHash}
                text={text}
              >
                <Home
                  style={{
                    color:
                      window.location.hash === homeHash
                        ? "rgb(51, 153, 255)"
                        : "inherit",
                  }}
                />
              </DrawerTabs>
            )}
            {text === "SPOON Explorer" && (
              <DrawerTabs
                key={`SPOON_Explorer_${index}`}
                to={explorerLink}
                hash={explorerHash}
                text={text}
              >
                <ZoomInIcon
                  style={{
                    color:
                      window.location.hash === explorerHash
                        ? "rgb(51, 153, 255)"
                        : "inherit",
                  }}
                />
              </DrawerTabs>
            )}
            {text === "SPOON Watcher" && (
              <DrawerTabs
                key={`SPOON_Watcher_${index}`}
                to={watcherLink}
                hash={watcherHash}
                text={text}
              >
                <RemoveRedEyeIcon
                  style={{
                    color:
                      window.location.hash === watcherHash
                        ? "rgb(51, 153, 255)"
                        : "inherit",
                  }}
                />
              </DrawerTabs>
            )}
            {text === "My watchlist" && (
              <ListItem key={`Watchlist_${index}`} button>
                <ListItemIcon>
                  {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
                </ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            )}
          </div>
        ))}
      </List>
      <Divider />
      <List>
        {["Mute Sound", "About Service Levels", "Warning", "Changelog"].map(
          (text, index) => (
            <div>
              {text === "Mute Sound" && (
                <DrawerTabs text={text}>
                  <SoundRizer />
                </DrawerTabs>
              )}
              {text === "About Service Levels" && (
                <DrawerTabs
                  key={`About_${index}`}
                  to={serviceLink}
                  hash={serviceHash}
                  text={text}
                >
                  <LocalOfferIcon
                    style={{
                      color:
                        window.location.hash === serviceHash
                          ? "rgb(51, 153, 255)"
                          : "inherit",
                    }}
                  />
                </DrawerTabs>
              )}
              {text === "Warning" && (
                <DrawerTabs
                  key={`Warning_${index}`}
                  to={warningLink}
                  hash={warningHash}
                  text={text}
                >
                  <InfoIcon
                    style={{
                      color:
                        window.location.hash === warningHash
                          ? "rgb(51, 153, 255)"
                          : "inherit",
                    }}
                  />
                </DrawerTabs>
              )}
              {text === "Changelog" && (
                <DrawerTabs to={ChangelogLink} hash={ChangelogHash} text={text}>
                  <HistoryIcon
                    style={{
                      color:
                        window.location.hash === ChangelogHash
                          ? "rgb(51, 153, 255)"
                          : "inherit",
                    }}
                  />
                </DrawerTabs>
              )}
            </div>
          )
        )}
      </List>
    </Drawer>
  );
};
export default withRouter(AppDrawer);
