import { EventType } from '../../models'

const OWNERSHIP_RENOUNCED_EVENT_TYPE_ID = 'ownership_renounced'

const ownershipRenouncedEventType = new EventType(
  OWNERSHIP_RENOUNCED_EVENT_TYPE_ID,
  'Ownership Renounced',
)

const ownershipRenouncedTip =
  " The token owner has renounced to its ownership. This means that all contract methods marked as 'onlyOwner' cannot be invoked anymore. Renouncing ownership is not a sign of a trustworthy token but it might help with credibility."

export {
  OWNERSHIP_RENOUNCED_EVENT_TYPE_ID,
  ownershipRenouncedEventType,
  ownershipRenouncedTip,
}
