import { Grid } from "@material-ui/core";
import { TokenAssessmentTitle } from "./TokenAssessmentTitle";
import { TokenAssessmentContent } from "./TokenAssessmentContent";
import { TokenAssessmentFooter } from "./TokenAssessmentFooter";

export const TokenAssessmentPopupTemplate = ({ token, onClose }) => {
  return (
    <Grid container xs={12}>
      <TokenAssessmentTitle
        symbol={token.symbol}
        name={token.name}
        riskLevel={token.riskLevel}
        onClose={onClose}
      />
      <TokenAssessmentContent token={token} />
      <TokenAssessmentFooter />
    </Grid>
  );
};
