import React from 'react'

const DYORContext = React.createContext()

const DYORProvider = ({ children }) => {
  const [dontShowAgain, setDontShowAgain] = React.useState(false)
  const [openDYOR, setOpenDYOR] = React.useState(false)
  const [tradeLink, setTradeLink] = React.useState('')

  const handleDYOR = (openDYOR, tradeLink = '') => {
    setOpenDYOR(openDYOR)
    setTradeLink(tradeLink)
  }

  const handleDontShowAgain = () => setDontShowAgain(dontShowAgain => !dontShowAgain)

  const value = React.useMemo(
    () => ({
      dontShowAgain,
      openDYOR,
      tradeLink,
      handleDYOR,
      handleDontShowAgain
    }),
    [dontShowAgain, openDYOR, tradeLink]
  );

  return (
    <DYORContext.Provider value={value}>
      {children}
    </DYORContext.Provider>
  );
}

const DYORConsumer = DYORContext.Consumer

export { DYORProvider, DYORConsumer, DYORContext }
