import { parseMinutes } from '../utils';

export class Event {
  #time
  #txHash
  #blockNumber
  #eventType

  constructor (txHash, blockNumber, eventType) {
    const now = new Date();
    this.#time = `${now.getHours()}:${parseMinutes(now.getMinutes())}`;
    this.#txHash = txHash;
    this.#blockNumber = blockNumber;
    this.#eventType = eventType;
  }

  get time() {
    return this.#time;
  }

  get txHash() {
    return this.#txHash;
  }

  set txHash(th) {
    this.#txHash = th;
  }

  get blockNumber() {
    return this.#blockNumber;
  }

  set blockNumber(bn) {
    this.#blockNumber = bn;
  }
    
  get eventType() {
    return this.#eventType;
  }

  set eventType(et) {
    this.#eventType = et;
  }
}