import {
  Box,
  Paper,
  Button,
  Grid,
  Typography,
  makeStyles,
} from "@material-ui/core";

const useStyles = makeStyles({
  "@keyframes slideButtonBackground": {
    to: {
      backgroundPosition: "20vw",
    },
  },
  button: {
    minHeight: "128px",
    background:
      "linear-gradient(180deg, rgba(165,216,255,1) 0%, rgba(58,122,190,1) 35%, rgba(20,91,167,1) 100%);",
    borderRadius: "4px",
    cursor: "pointer",
    "&:hover": {
      backgroundImage:
        "linear-gradient(90deg, #00C0FF 0%, #FFCF00 49%, #FC4F4F 80%, #00C0FF 100%)",
      animation: "$slideButtonBackground 5s linear infinite",
    },
  },
  buttonLogo: {
    cursor: "pointer",
    width: "64px",
    height: "64px",
  },
  appTitle: {
    fontSize: "18px",
    fontWeight: "400",
  },
  appDescription: {
    fontSize: "13px",
  },
});

export const ThirdPartyAppCard = ({ appDefinition, contractAddress }) => {
  const classes = useStyles();
  const url = appDefinition.buildLinkFunction(contractAddress);

  return (
    <Box style={{ padding: "10px" }}>
      <Paper elevation={4}>
        <Button
          className={classes.button}
          href={url}
          target="_blank"
          rel="noopener"
        >
          <Grid container xs={12} spacing={3}>
            <Grid item xs="auto">
              <img
                src={appDefinition.iconRelativeUrl}
                alt={appDefinition.appTitle}
                className={classes.buttonLogo}
              />
            </Grid>
            <Grid item xs={9}>
              <Typography className={classes.appTitle} variant="h6">
                {appDefinition.appTitle}
              </Typography>
              <Typography className={classes.appDescription}>
                {appDefinition.appDescription}
              </Typography>
            </Grid>
          </Grid>
        </Button>
      </Paper>
    </Box>
  );
};
