import React from "react";
import ContractIcon from "../../../../../../img/ContractIcon.svg";
import { LinkButtonWithLogo } from "../LinkButtonWithLogo";

export const VerifyContractButton = ({
  contractAddress,
  alreadyClicked,
  onClick,
}) => {
  const url = `https://bscscan.com/address/${contractAddress}#code`;
  const text = "Verify Contract";

  return (
    <LinkButtonWithLogo
      onClick={onClick}
      href={url}
      logo={ContractIcon}
      alt={text}
      isWarning={!alreadyClicked}
    >
      {text}
    </LinkButtonWithLogo>
  );
};
