import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import YouTube from "react-youtube";
import CoinSpin from "../../../img/CoinSpin.gif";
import Button from "@material-ui/core/Button";
import RemoveRedEyeIcon from "@material-ui/icons/RemoveRedEye";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import { LinkRouter } from "../../common/LinkRouter";
import { watcherLink, explorerLink } from "../../../lib/constants";
import { version } from "../../../../package.json";

const useStyles = makeStyles(() => ({
  container: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    height: "100%",
  },
  containerRow: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    height: "100%",
  },
  typo: {
    marginTop: "20px",
    marginBottom: "20px",
  },
  typoRowWatcher: {
    marginTop: "20px",
    marginBottom: "20px",
    marginRight: "70px",
  },
  typoRowExplorer: {
    marginTop: "20px",
    marginBottom: "20px",
    marginLeft: "70px",
    marginRight: "120px",
  },
  spincoin: {
    width: "80px",
    marginTop: "5px",
    marginLeft: "400px",
    position: "absolute",
  },
  spincoin2: {
    width: "80px",
    marginTop: "5px",
    marginLeft: "-400px",
    position: "absolute",
  },
  buttons: {
    marginTop: "20px",
    marginBottom: "20px",
    marginLeft: "70px",
    marginRight: "70px",
    color: "black",
  },
  icons: {
    marginLeft: "15px",
    color: "rgb(51, 153, 255)",
  },
}));

export const HomePage = () => {
  const classes = useStyles();
  const opts = {
    height: "390",
    width: "640",
    playerVars: {
      autoplay: 1,
    },
  };

  return (
    <>
      <div className={classes.container}>
        <img src={CoinSpin} className={classes.spincoin} alt="SpinCoin" />
        <img src={CoinSpin} className={classes.spincoin2} alt="SpinCoin" />
        <Typography variant="h4" className={classes.typo}>
          Stop getting rug pulled and scammed
        </Typography>
        <YouTube videoId="RJey2m8Ky7Y" opts={opts} />
      </div>
      <div className={classes.container}>
        <div className={classes.containerRow}>
          <Typography variant="h5" className={classes.typoRowWatcher}>
            Automatic risk assessment with The Watcher
          </Typography>
          <Typography variant="h5" className={classes.typoRowExplorer}>
            Absolute control with The Explorer
          </Typography>
        </div>
        <div className={classes.containerRow}>
          <LinkRouter to={watcherLink} className={classes.buttons}>
            <Button color="black" variant="outlined">
              Go to Watcher <RemoveRedEyeIcon className={classes.icons} />
            </Button>
          </LinkRouter>
          <LinkRouter to={explorerLink} className={classes.buttons}>
            <Button color="black" variant="outlined">
              Go to Explorer <ZoomInIcon className={classes.icons} />
            </Button>
          </LinkRouter>
        </div>
        <div className={classes.containerRow}>
          <Typography variant="h5" className={classes.typo}>
            All powered by our SPOON Voyager Engine {version}
          </Typography>
        </div>
      </div>
    </>
  );
};
