import { Button } from "@material-ui/core";
import { useTheme } from "@material-ui/core";
import { LinkButtonWithLogo } from "../LinkButtonWithLogo";
import HoneypotDetectorLogo from "../../../../../../img/HoneypotDetector.png";

export const HoneypotDetectorButton = ({
  contractAddress,
  buttonStyle,
  alreadyClicked,
  onClick,
}) => {
  const href = `https://honeypot.is/?address=${contractAddress}`;

  return (
    <LinkButtonWithLogo
      href={href}
      logo={HoneypotDetectorLogo}
      alt={contractAddress}
      buttonStyle={buttonStyle}
      isWarning={!alreadyClicked}
      onClick={onClick}
    >
      Check for Honeypot
    </LinkButtonWithLogo>
  );
};
