import { Button } from "@material-ui/core";
import { useTheme, makeStyles } from "@material-ui/core";

const useStyles = makeStyles({
  "@keyframes viewMoreHover": {
    to: {
      backgroundPosition: "20vw",
    },
  },
  button: {
    cursor: "pointer",
    "&:hover": {
      backgroundImage:
        "linear-gradient(90deg, #00C0FF 0%, #FFCF00 49%, #FC4F4F 80%, #00C0FF 100%)",
      animation: "$viewMoreHover 5s linear infinite",
    },
  },
});

export const TokenCardViewMoreButton = ({ contractAddress, onClick }) => {
  const theme = useTheme();
  const classes = useStyles();

  return (
    <Button
      onClick={onClick}
      target="_blank"
      rel="noopener"
      className={classes.button}
      style={{
        fontSize: "15px",
        width: "100%",
        color: theme.palette.primary.main,
        backgroundColor: theme.palette.text.secondary,
        fontWeight: "bold",
      }}
      variant="contained"
      color="textSecondary"
    >
       View More
    </Button>
  );
};
