import React from "react";
import Paper from "@material-ui/core/Paper";
import Typography from "@mui/material/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Table from "./Table";
import { TokenHandlerConsumer } from "../../../lib/tokenHandler";
import { LinkRouter } from "../../common/LinkRouter";
import { watcherLink } from "../../../lib/constants";
import Alert from "@material-ui/lab/Alert";
import { LoadingProcess } from "../../common/Loadings";

const useStyles = makeStyles({
  root: {
    width: "100%",
  },
});

const Explorer = () => {
  const classes = useStyles();
  return (
    <>
      <LinkRouter to={watcherLink}>
        <Alert variant="filled" severity="info">
          This is the Explorer. Click here for risk assessment at the Watcher.
        </Alert>
      </LinkRouter>
      <TokenHandlerConsumer>
        {(value) => {
          const { tokens, errorMessage } = value;
          return (
            <div>
              <header>
                <p>{errorMessage}</p>
                <Typography variant="h3" component="h3">
                  SPOON Explorer
                </Typography>
                {tokens.length === 0 ? (
                  <LoadingProcess />
                ) : (
                  <>
                    <Paper className={classes.root}>
                      <Table tokens={tokens} singleTokenMode={false} />
                    </Paper>
                  </>
                )}
              </header>
            </div>
          );
        }}
      </TokenHandlerConsumer>
    </>
  );
};

export default Explorer;
