import { red } from '@material-ui/core/colors'
import { createTheme } from '@material-ui/core/styles'

const primary = 'rgb(0, 30, 60)'
const background = 'rgb(10, 25, 41)'
const navbarBackground = 'rgb(51, 153, 255)'
const secondary = 'rgb(165, 216, 255)'

// A custom theme for this app
const theme = createTheme({
  typography: {
    fontFamily: 'Poppins',
  },
  palette: {
    type: 'dark',
    primary: {
      main: navbarBackground,
      navbarBackground,
      priceTag: primary,
      blue: '#3f51b5',
    },
    text: {
      secondary,
    },
    secondary: {
      main: '#EFD4FF',
    },
    warning: {
      main: '#ff9800',
    },
    footer: {
      background: primary,
    },
    error: {
      main: red.A400,
    },
    background: {
      default: background,
      paper: primary,
    },
  },
})

export default theme
