import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";

const useStyles = makeStyles(() => ({
  tokenCardNameContainer: {
    marginLeft: "auto",
    marginRight: "auto",
    textAlign: "center",
  },
  tokenCardNameText: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis",
    fontSize: "10px",
  },
}));

export const TokenCardName = ({ children }) => {
  const classes = useStyles();

  return (
    <div className={classes.tokenCardNameContainer}>
      <Typography className={classes.tokenCardNameText}>{children}</Typography>
    </div>
  );
};
