import React from 'react';
import { Typography } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import DropDown from '../../common/DropDown';
import RemoveRedEyeIcon from "@material-ui/icons/RemoveRedEye";
import AssessmentIcon from '@material-ui/icons/Assessment';
import Home from '@material-ui/icons/Home';
import WebIcon from '@material-ui/icons/Web';
import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive';
import Box from '@material-ui/core/Box';


export const Alpha12 = (props) => {
  const className = props.className;
  return (
    <Grid className={className}>
      <DropDown name="SPOON ALPHA 0.1.2">
        <Typography>
          <Box>
            <AssessmentIcon/> - Added changelog section
          </Box>
          <Box>
            <Home/> - Promotional video on home page
          </Box>
          <Box>
            <RemoveRedEyeIcon/> - Added sound feedback when token has added on watcher with on/off configuration
          </Box>
          <Box>
            <NotificationsActiveIcon/> - Improved snackbars performance
          </Box>
          <Box>
            <WebIcon/> - Overall UI and UX improvements
          </Box>
        </Typography>
      </DropDown>
    </Grid>
  );
};