import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Link from "@material-ui/core/Link";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import { Alert, AlertTitle } from "@material-ui/lab";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemIcon from "@mui/material/ListItemIcon";
import InfoIcon from "@material-ui/icons/Info";
import { ModalBase } from "./ModalBase";
import { DYORConsumer } from "../../../lib/dyor";
import { tokenAssessmentTips } from "../../../lib/constants";

const useStyles = makeStyles((theme) => ({
  btn: {
    color: theme.palette.primary.blue,
    borderRadius: "4px",
    border: "1px solid rgba(0, 0, 0, 0.23)",
    padding: "5px 15px",
  },
  error: {
    margin: "5px",
    color: theme.palette.error.main,
  },
  check: {
    color: "#000",
  },
  marginBottom: {
    marginBottom: "5px",
  },
}));

export const DYOR = () => {
  const classes = useStyles();

  return (
    <DYORConsumer>
      {(value) => {
        const { openDYOR, tradeLink, handleDYOR, handleDontShowAgain } = value;
        return (
          <ModalBase open={openDYOR} onClose={() => handleDYOR(false)}>
            <Alert variant="outlined" severity="warning">
              <div className={classes.marginBottom}>
                <AlertTitle>Please DO YOUR OWN RESEARCH:</AlertTitle>
                <List>
                  {tokenAssessmentTips.map((x, index) => (
                    <ListItem
                      key={`Tip_${index}`}
                      component="li"
                      divider
                      disablePadding
                    >
                      <ListItemIcon>
                        <InfoIcon />
                      </ListItemIcon>
                      <ListItemText className={classes.check} primary={x} />
                    </ListItem>
                  ))}
                </List>
              </div>
              <FormControlLabel
                className={classes.check}
                control={<Checkbox onClick={handleDontShowAgain} />}
                label="Do not show it again."
              />
              <br />
              <Button
                variant="outlined"
                className={classes.error}
                onClick={() => handleDYOR(false)}
              >
                Cancel
              </Button>
              <Link
                target="_blank"
                rel="noopener"
                variant="button"
                underline="none"
                className={classes.btn}
                href={tradeLink}
                component={Button}
                onClick={() => handleDYOR(false)}
              >
                OK
              </Link>
            </Alert>
          </ModalBase>
        );
      }}
    </DYORConsumer>
  );
};
