class WhitelistedAddress {
  constructor(contractAddress, name, network = "BSC") {
    this.contractAddress = contractAddress;
    this.name = name;
    this.network = network;
  }
}

export const Whitelister = {
  getWhitelistedAddresses: async () => {
    const addresses = Promise.resolve({
      "0X12A973B1E485CB08F517EECBE32FE82EAEB94E3C": new WhitelistedAddress(
        "0X12A973B1E485CB08F517EECBE32FE82EAEB94E3C",
        "SPOON"
      ),
      "0X037838B556D9C9D654148A284682C55BB5F56EF4": new WhitelistedAddress(
        "0X037838B556D9C9D654148A284682C55BB5F56EF4",
        "LIGHT"
      ),
      "0XB09FE1613FE03E7361319D2A43EDC17422F36B09": new WhitelistedAddress(
        "0XB09FE1613FE03E7361319D2A43EDC17422F36B09",
        "BOG"
      ),
      "0XB27ADAFFB9FEA1801459A1A81B17218288C097CC": new WhitelistedAddress(
        "0XB27ADAFFB9FEA1801459A1A81B17218288C097CC",
        "POOCOIN"
      ),
    });

    return addresses;
  },
};
