import React from "react";
import { Grid, Paper, makeStyles } from "@material-ui/core";
import { TokenAssessmentContentContainer } from "../TokenAssessmentContentContainer";
import { ContractAddressButton } from "./ContractAddressButton";
import { VerifyContractButton } from "./VerifyContractButton";
import { CheckTokenHoldersButton } from "./CheckTokenHoldersButton";
import { CheckLPHoldersButton } from "./CheckLPHoldersButton";
import { TokenCardChartButton } from "../TokenCardChartButton";
import { TokenCardBuyAtPancakeButton } from "../TokenCardBuyAtPancakeButton";
import { TokenCardBuyAtPoocoinButton } from "../TokenCardBuyAtPoocoinButton";
import { TokenPriceIndicator } from "./TokenPriceIndicator";
import { HoneypotDetectorButton } from "./HoneypotDetectorButton";

const useStyles = makeStyles(() => ({
  summaryDescription: {
    fontSize: "16px",
  },
}));

export const SummaryTab = ({ token }) => {
  const classes = useStyles();

  const [honeypotDetectorClicked, setHoneypotDetectorClicked] =
    React.useState(false);
  const [verifyContractClicked, setVerifyContractClicked] =
    React.useState(false);
  const [checkHoldersClicked, setCheckHoldersClicked] = React.useState(false);
  const [checkLPHoldersClicked, setCheckLPHoldersClicked] =
    React.useState(false);

  const onClickCurry = (setFunction) => () => setFunction(true);

  const { contractAddress, LPAddress } = token;

  const riskAnalysisSummaryDescription = token.riskAnalysisSummary.map(
    (text) => <p>{text}</p>
  );

  return (
    <TokenAssessmentContentContainer>
      <Grid container xs={12}>
        <Grid item xs={5}>
          <Grid container xs={12} spacing={3}>
            <Grid item xs={12}>
              <HoneypotDetectorButton
                contractAddress={contractAddress}
                alreadyClicked={honeypotDetectorClicked}
                onClick={onClickCurry(setHoneypotDetectorClicked)}
              />
            </Grid>
            <Grid item xs={12}>
              <ContractAddressButton contractAddress={contractAddress} />
            </Grid>
            <Grid item xs={6}>
              <TokenPriceIndicator contractAddress={contractAddress} />
            </Grid>
            <Grid item xs={6}>
              <VerifyContractButton
                contractAddress={contractAddress}
                alreadyClicked={verifyContractClicked}
                onClick={onClickCurry(setVerifyContractClicked)}
              />
            </Grid>
            <Grid item xs={6}>
              <CheckTokenHoldersButton
                contractAddress={contractAddress}
                alreadyClicked={checkHoldersClicked}
                onClick={onClickCurry(setCheckHoldersClicked)}
              />
            </Grid>
            <Grid item xs={6}>
              <CheckLPHoldersButton
                LPAddress={LPAddress}
                alreadyClicked={checkLPHoldersClicked}
                onClick={onClickCurry(setCheckLPHoldersClicked)}
              />
            </Grid>
            <Grid item xs={4}>
              <TokenCardChartButton contractAddress={contractAddress} />
            </Grid>
            <Grid item xs={4}>
              <TokenCardBuyAtPancakeButton contractAddress={contractAddress} />
            </Grid>
            <Grid item xs={4}>
              <TokenCardBuyAtPoocoinButton contractAddress={contractAddress} />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={7}>
          <Paper
            elevation={2}
            style={{ paddingTop: "10px", paddingBottom: "10px" }}
            className={classes.summaryDescription}
          >
            {riskAnalysisSummaryDescription}
          </Paper>
        </Grid>
      </Grid>
    </TokenAssessmentContentContainer>
  );
};
