import { GlobalState } from '../global'
import { liquidityCreationEventType, exchangeRouters } from '../constants'
import {
  safeLowerCase,
  buildAsTokenContract,
  areBothCommonTokens,
  isCommonToken,
} from '../operators'
import logger from '../logger'

export class LiquidityCreatedHunter {
  isTransactionTargetable(pkg) {
    const hasTo = !!pkg.transaction.to
    const hasStartingHex =
      pkg.transaction.input && pkg.transaction.input.startsWith('0xf305d719')

    // Quick checks to perform
    if (!hasTo || !hasStartingHex) return false

    // Liq. creation targets a router contract
    const targetIsRouter = exchangeRouters.some(
      (r) => r.toLowerCase() === pkg.transaction.to.toLowerCase(),
    )
    return targetIsRouter
  }

  async isEventConfirmed(pkg) {
    await pkg.ensureReceipt()

    if (!pkg.receipt) return false
    if (!pkg.receipt.logs) return false

    const { logs } = pkg.receipt

    // We need to have some specific logs before calling it liquidity created
    if (!logs) return false
    if (logs.length !== 9) return false

    // If we have no address in log 5 its not what we are looking for
    if (!logs[5].address) return false

    // Get the address of logs[5]
    const logs5Addr = safeLowerCase(logs[5].address)
    if (!logs5Addr) return false

    const addressesThatMustBeTheSame = [
      safeLowerCase(logs[5].address),
      safeLowerCase(logs[6].address),
      safeLowerCase(logs[7].address),
      safeLowerCase(logs[8].address),
    ]

    // If all addresses are the same in indexes 5, 6, 7 and 8 we can confirm this is liq created
    const allAddressesAreTheSame = addressesThatMustBeTheSame.every(
      (a) => a === logs5Addr,
    )

    return allAddressesAreTheSame
  }

  async present(pkg) {
    await pkg.ensureAsCakeLpContract(5)

    // Not the kind of contract we expected! Cannot process
    const isPancakeLpContract = await pkg.contract.isCakeLPContract()
    if (!isPancakeLpContract) {
      return
    }

    // While other strategies could avoid getting both address, this is a simpler approach
    const token0Address = await pkg.contract.getToken0Address()
    const token1Address = await pkg.contract.getToken1Address()

    // We do not process this as both are common tokens
    const areBothCommon = areBothCommonTokens(token0Address, token1Address)
    if (areBothCommon) {
      return
    }

    // Define which token address we care about
    let ourTokenAddress = token0Address
    if (isCommonToken(ourTokenAddress)) ourTokenAddress = token1Address

    const tokenContract = await buildAsTokenContract(
      pkg.connector,
      ourTokenAddress,
    )

    // Get token description for console logging
    const description = await tokenContract.getDescription()
    const symbol = await tokenContract.getSymbol()
    const name = await tokenContract.getName()
    const isMintable = await tokenContract.isMintable()

    // A null description means this is not an usual token contract and we cannot scan it
    if (!description) return

    GlobalState.Observer.emitTokenEvent(
      ourTokenAddress,
      pkg.contract.contract._address,
      symbol,
      name,
      isMintable,
      pkg.transaction.hash,
      pkg.receipt.blockNumber,
      liquidityCreationEventType,
    )

    GlobalState.showExplorerEvent(symbol, 'Liquidity Created')

    logger.info({
      evt: `Liquidity Created - ${description}`,
      hash: pkg.transaction.hash,
      tx: pkg.transaction,
    })
  }
}
