export class EventType {
  #identifier
  #name

  constructor (identifier, name) {
    this.#identifier = identifier;
    this.#name = name;
  }

  get identifier() {
    return this.#identifier;
  }

  set identifier(id) {
    this.#identifier = id;
  }

  get name() {
    return this.#name;
  }

  set name(n) {
    this.#name = n;
  }
}