import React from 'react';
import { Typography } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import DropDown from '../../common/DropDown';
import RemoveRedEyeIcon from "@material-ui/icons/RemoveRedEye";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import AssessmentIcon from '@material-ui/icons/Assessment';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import BuildIcon from '@material-ui/icons/Build';
import Box from '@material-ui/core/Box';

export const Alpha = (props) => {
  const className = props.className;
  return (
    <Grid className={className}>
      <DropDown name="SPOON ALPHA 0.1.0">
        <Typography>
          <Box>
            <BuildIcon/> - Basic toolset to try SPOON
          </Box>
          <Box>
            <FiberManualRecordIcon/> - Live events feed
          </Box>
          <Box>
            <AssessmentIcon/> - Basic risk assessment
          </Box>
          <Box>
            <ZoomInIcon/> - SPOON Explorer to see all new activity on the blockchain
          </Box>
          <Box>
            <RemoveRedEyeIcon/> - SPOON Watcher to see activity of currently tradeable tokens 
          </Box>
        </Typography>
      </DropDown>
    </Grid>
  );
};