import React from "react";

import { Alert, AlertTitle } from "@mui/material";

export const TokenAssessmentTip = ({
  children,
  title = "General Tips",
  height = "125px",
}) => {
  return (
    <Alert sx={{ height }} severity="info">
      {title && <AlertTitle>{title}</AlertTitle>}
      {children}
    </Alert>
  );
};
