import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableContainer from "@material-ui/core/TableContainer";
import TableHeadRow from "../../../template/TableHeadRow";
import TableEvents from "./TableEvents";

const useStyles = makeStyles({
  container: {
    height: "auto",
    maxHeight: "70vh",
    overflow: "auto",
  },
});

const cellTitles = [
  "Time",
  "Block",
  "Token",
  "Contract",
  "LP Holders",
  "Event",
  "",
  "Tx Hash",
  "Trade",
];

const cellTitlesForSingleToken = ["Time", "Block", "Event", "", "Tx Hash"];

const ExplorerTable = (props) => {
  const classes = useStyles();
  const tableHeaders = !!props.singleTokenMode
    ? cellTitlesForSingleToken
    : cellTitles;

  return (
    <TableContainer className={classes.container}>
      <Table stickyHeader aria-label="explorer table">
        <TableHeadRow cellTitles={tableHeaders} />
        <TableEvents
          tokens={props.tokens}
          singleTokenMode={props.singleTokenMode}
        />
      </Table>
    </TableContainer>
  );
};

export default ExplorerTable;
