import React, { PureComponent } from "react";
import { Box } from "@material-ui/core";
import { tokenAssessmentTips as tips } from "../../../../../lib/constants";
import { getRandomInteger } from "../../../../../lib/math";
import { TokenAssessmentTip } from "./TokenAssessmentTip";

const carouselDelayInMilliseconds = 8000;

export class TokenAssessmentTipsCarousel extends PureComponent {
  constructor(props) {
    super(props);

    this.currentTipIndex = getRandomInteger(0, tips.length - 1);
    this.refreshInterval = setInterval(
      this.nextTip,
      carouselDelayInMilliseconds
    );

    this.state = {
      currentTip: tips[this.currentTipIndex],
    };
  }

  componentWillUnmount() {
    clearInterval(this.refreshInterval);
  }

  nextTip = () => {
    let nextTipIndex = this.currentTipIndex + 1;
    if (nextTipIndex >= tips.length) nextTipIndex = 0;

    const nextTip = tips[nextTipIndex];

    this.currentTipIndex = nextTipIndex;
    this.setState({
      currentTip: nextTip,
    });
  };

  render() {
    const { currentTip } = this.state;

    return (
      <Box
        sx={{
          minHeight: "125px",
          paddingLeft: "20px",
          paddingRight: "20px",
          paddingBottom: "20px",
        }}
      >
        <TokenAssessmentTip>{currentTip}</TokenAssessmentTip>
      </Box>
    );
  }
}
