import { Event } from './event'
import { RiskLevelAnalyser } from '../tokenHandler/RiskLevelAnalyser'

export class Token {
  #contractAddress
  #LPAddress
  #symbol
  #name
  #isMintable
  #events
  #riskLevel
  #newEvent

  constructor(contractAddress, LPAddress, symbol, name, isMintable, newEvent) {
    this.#contractAddress = contractAddress
    this.#LPAddress = LPAddress
    this.#symbol = symbol
    this.#name = name
    this.#isMintable = isMintable
    this.#events = []
    this.#riskLevel = null
    this.#newEvent = newEvent
  }

  get contractAddress() {
    return this.#contractAddress
  }

  set contractAddress(ca) {
    this.#contractAddress = ca
  }

  get symbol() {
    return this.#symbol
  }

  set symbol(s) {
    this.#symbol = s
  }

  get name() {
    return this.#name
  }

  set name(n) {
    this.#name = n
  }

  get LPAddress() {
    return this.#LPAddress
  }

  set LPAddress(lp) {
    this.#LPAddress = lp
  }

  get events() {
    return this.#events
  }

  get riskLevel() {
    return this.#riskLevel
  }

  set riskLevel(riskLevel) {
    this.#riskLevel = riskLevel
  }

  get newEvent() {
    return this.#newEvent
  }

  set newEvent(newEvent) {
    this.#newEvent = newEvent
  }

  get isMintable() {
    return this.#isMintable
  }

  set isMintable(isMintable) {
    this.#isMintable = isMintable
  }

  pushEvent(event) {
    this.#newEvent = true
    this.#events.push(event)
    this.assessRiskLevel()
  }

  addNewEvent(txHash, blockNumber, eventType) {
    const event = new Event(txHash, blockNumber, eventType)
    this.pushEvent(event)
  }

  assessRiskLevel() {
    const [riskLevel, riskAnalysisSummary] = RiskLevelAnalyser.runAssessment(
      this,
    )
    this.riskLevel = riskLevel
    this.riskAnalysisSummary = riskAnalysisSummary
  }
}
