import log from 'loglevel';
import remote from 'loglevel-plugin-remote';
import * as _ from 'lodash';

class Logger {
    #logUrl
    #logRemote

    constructor(envLogLevel) {
        this.#logUrl = process.env.REACT_APP_LOG_URL;
        this.#logRemote = process.env.REACT_APP_LOG_REMOTE || true;
        this.#setLevel(envLogLevel);
        this.#setRemote(envLogLevel);
    }

    trace(data) {
        if (log.getLevel() <= log.levels.TRACE) {
            log.trace(data);
        }
    }

    log(data) {
        if (log.getLevel() <= log.levels.DEBUG) {
            log.log(data);
        }
    }

    debug(data) {
        if (log.getLevel() <= log.levels.DEBUG) {
            log.debug(data);
        }
    }

    info(data) {
        log.info(data);
    }

    warn(data) {
        log.warn(data);
    }

    error(message) {
        log.error(message);
    }

    #setLevel(envLogLevel) {
        let logLevel = envLogLevel || log.levels.ERROR;
        try {
            log.setLevel(_.toNumber(logLevel), false);
        } catch (error) {
            log.setLevel(log.levels.ERROR, false);
        }
    }

    #setRemote() {
        if (this.#logRemote) {
            const customJSON = log => ({
                project: 'SPOON',
                level: log.level.label,
                message: log.message,
                stacktrace: log.stacktrace
            });
               
            const config = {
                url: this.#logUrl,
                method: 'POST',
                level: 'error',
                timeout: 0,
                capacity: 25,
                stacktrace: {
                    levels: ['trace', 'warn', 'error'],
                    depth: 5,
                    excess: 0,
                },
                timestamp: () => new Date().toISOString(),
                format: customJSON,
            };

            remote.apply(log, config);
        }
    }
}

const logger = new Logger(process.env.REACT_APP_LOG_LEVEL);

export default logger;