import React from "react";
import { withRouter } from "react-router-dom";
import { useTheme, makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import Button from "@material-ui/core/Button";
import clsx from "clsx";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import AppBar from "@material-ui/core/AppBar";
import CircularProgress from "@material-ui/core/CircularProgress";
import { drawerWidth, buySPOONUrl } from "../../../lib/constants";
import { withBlocksExecuted } from "../../../lib/blocksExecuted";
import Price from "../../common/Price";

const useStyles = makeStyles((theme) => ({
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: "none",
  },
  link: {
    marginRight: theme.spacing(2),
  },
  serviceLevel: {
    marginLeft: "auto",
  },
  navbarRight: {
    marginLeft: "auto",
  },
  circularRoot: {
    display: "flex",
    "& > * + *": {
      marginLeft: theme.spacing(2),
    },
  },
  blocksExecutedContainer: {
    display: "inline-table",
  },
  blocksExecutedSpan: {
    display: "table-cell",
    verticalAlign: "middle",
    paddingLeft: "20px",
  },
}));

const getMiningStatus = (blocksExecutedInfo) => {
  if (!blocksExecutedInfo.startBlock) return "";

  const miningStatus = `Mining blocks: ${blocksExecutedInfo.currentBlock} / ${blocksExecutedInfo.latestBlock} `;
  return miningStatus;
};

const NavbarComponent = (props) => {
  const { open, setOpen, blocksExecutedInfo } = props;
  const classes = useStyles();
  const theme = useTheme();
  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const miningStatus = getMiningStatus(blocksExecutedInfo);

  return (
    <AppBar
      position="fixed"
      className={clsx(classes.appBar, {
        [classes.appBarShift]: open,
      })}
    >
      <Toolbar>
        <IconButton
          color="inherit"
          aria-label="open drawer"
          onClick={handleDrawerOpen}
          edge="start"
          className={clsx(classes.menuButton, {
            [classes.hide]: open,
          })}
        >
          <MenuIcon />
        </IconButton>
        <Typography className={classes.link}>
          <Button
            href={buySPOONUrl}
            target="_blank"
            style={{
              fontSize: "15px",
              color: theme.palette.primary.main,
              backgroundColor: theme.palette.background.default,
            }}
            variant="contained"
            color="textSecondary"
          >
            Buy SPOON
          </Button>
        </Typography>

        <Price />

        <Typography className={classes.link}>
          <Link
            href="https://forms.gle/tz1Bsg5sLRZwj2gY8"
            target="_blank"
            style={{ color: "#000000" }}
          >
            Feedback
          </Link>
        </Typography>

        <Typography className={classes.link}>
          <Link
            href="https://t.me/spoon_token"
            target="_blank"
            style={{ color: "#000000" }}
          >
            Community
          </Link>
        </Typography>

        <Typography className={classes.link}>
          <Link
            href="https://www.spoontoken.com/"
            target="_blank"
            style={{ color: "#000000" }}
          >
            Marketing Site
          </Link>
        </Typography>

        <Typography className={classes.navbarRight}>
          <span className={classes.blocksExecutedContainer}>
            <CircularProgress color="textSecondary" />
            <span className={classes.blocksExecutedSpan}>{miningStatus}</span>
          </span>

          {/* <LinkRouter to='/servicelevelindicator'>[FREE]</LinkRouter> */}
        </Typography>
      </Toolbar>
    </AppBar>
  );
};
export const Navbar = withBlocksExecuted(withRouter(NavbarComponent));
