import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Typography, Button } from "@material-ui/core";

const useStyles = makeStyles({
  pointer: {
    cursor: 'pointer',
  }
});

export const TradeButton = (props) => {
  const classes = useStyles();
  const { href, img, className, alt, title, handleDYOR } = props;

  return (
    <Button onClick={() => handleDYOR(true, href)}>
      <Typography>{title}&nbsp;&nbsp;</Typography>
      <img
        src={img}
        className={`${className} ${classes.pointer}`}
        alt={alt} />
    </Button>
  );
};