import { PureComponent } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";
import { GlobalState } from "./lib/global";
import { DYOR } from "./components/common/Modals";
import Router from "./Router";
import theme from "./theme";
import { TokenHandlerProvider } from "./lib/tokenHandler";
import { DYORProvider } from "./lib/dyor";
import { Voyager } from "./lib/voyager";
import { rotatorFactory } from "./lib/rotator";
import { enabledEventHunters } from "./lib/eventHunter";
import { BlocksExecutedContext } from "./lib/blocksExecuted";
import { Snackbar } from "./components/common/Snackbar";
import logger from "./lib/logger";
import { Whitelister } from "./lib/whitelister/Whitelister";

export class App extends PureComponent {
  constructor(props) {
    super(props);

    GlobalState.AppComponent = this;

    this.state = {
      blocksExecutedInfo: {
        startBlock: null,
        currentBlock: null,
        latestBlock: null,
      },
    };
  }

  async componentDidMount() {
    GlobalState.WhitelistedAddresses =
      await Whitelister.getWhitelistedAddresses();
    await this.runVoyager();
  }

  async runVoyager() {
    try {
      const [rotator, defaultConnector] = rotatorFactory();

      const voyager = new Voyager(
        rotator,
        defaultConnector,
        enabledEventHunters,
        process.env.REACT_APP_STARTING_BLOCK,
        process.env.REACT_APP_ENDING_BLOCK
      );

      GlobalState.Voyager = voyager;
      await voyager.run();
    } catch (error) {
      logger.error(error.message);
      GlobalState.Observer.emitOpenSnackbar(error.message, "error");
      await this.runVoyager();
    }
  }

  setTokenHandlerState(tokenHandlerResult) {
    this.setState({ tokenHandlerResult });
  }

  setBlocksExecuted(blocksExecutedInfo) {
    this.setState({ blocksExecutedInfo });
  }

  render() {
    const { blocksExecutedInfo } = this.state;

    return (
      <ThemeProvider theme={theme}>
        {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
        <CssBaseline />
        <TokenHandlerProvider>
          <DYORProvider>
            <DYOR />
            <BlocksExecutedContext.Provider value={blocksExecutedInfo}>
              <Snackbar>
                <Router />
              </Snackbar>
            </BlocksExecutedContext.Provider>
          </DYORProvider>
        </TokenHandlerProvider>
      </ThemeProvider>
    );
  }
}
